import React, {useState} from 'react'
import {
    Container,
    Row,
    Col,
    Button,
    NavLink,
    ButtonGroup,
    Tooltip,
    Input,
    InputGroup,
    Form,
    FormGroup, CardFooter, CardBody, Card
} from 'reactstrap'
import {FaInfoCircle, FaCheck} from 'react-icons/fa'
import './CheckoutLogin.scss'
import Message from "../ui/message/Message";
import ReactHtmlParser from "react-html-parser";

function CheckoutLogin(props){
    const text = (prop) => {
        if(props.language){
            return props.language[prop]
        }
    }

    const sendInfo = () => {
        console.log('loginForm')
        const loginForm = document.forms.loginForm
        const loginData = new FormData(loginForm)
        props.sendAuthData('login', loginData)
        return false
    }

    const continueCheckout = () => {
        props.openAllSections()
        return false
    }

    const register = () => {
        props.register()
        return false
    }

    const renderForm = () => {
        if(props.info.form) {
            const form = props.info.form
            return form.map((prop, key) => {
                return (
                    <FormGroup key={'login_' + key}>
                        <label>{prop.entry}</label>
                        <Input
                            id={'login_' + prop.name}
                            name={prop.name}
                            placeholder={prop.entry}
                            type={prop.type}
                        />
                    </FormGroup>
                )
            })
        } else {
            return null
        }
    }

    return(
        <Container fluid={true}>
        <Row id="login">
             <Col xs={{size: 12}} sm={{size: 6}}>
                <h2 className="subtitle">{text('text_new_customer')}</h2>
                <p>{ReactHtmlParser (text('text_register_account'))}</p>
                 {/*<Button*/}
                 {/*    className="theme-border-button"*/}
                 {/*    type="button"*/}
                 {/*    color="none"*/}
                 {/*    onClick={register}*/}
                 {/*>*/}
                 {/*    <span>{text('button_register')}</span>*/}
                 {/*</Button>*/}
                 <Button
                     className="theme-border-button"
                     type="button"
                     color="none"
                     onClick={continueCheckout}
                 >
                     <span>{text('button_continue')}</span>
                 </Button>
             </Col>
             <Col xs={{size: 12}} sm={{size: 6}}>
                 <h2 className="subtitle">{text('text_returning_customer')}</h2>
                 <Form name={'loginForm'}>
                     {renderForm()}
                 </Form>
                 <Message
                     success={props.messagesAuth.login}
                     error={props.errorsAuth.login}
                 />
                 <div className={'buttons'}>
                     <Button
                         className="theme-border-button"
                         type="button"
                         color="none"
                         onClick={sendInfo}
                     >
                         <span>{props.info.button}</span>
                     </Button>
                 </div>
             </Col>
        </Row>
        </Container>
    )
}

export default CheckoutLogin