import API from "../../utils/API"
import {
	FETCH_WISH_INFO_START,
	FETCH_WISH_INFO_SUCCESS,
	FETCH_WISH_INFO_ERROR
} from './actionTypes'
import {fetchCartInfo, fetchCartInfoError, fetchCartInfoStart, fetchCartInfoSuccess} from "./cart";

export function fetchWishInfo(callback = () => {}){
	const start = new Date()
	return async dispatch => {
		dispatch(fetchWishInfoStart())
		try{
			const responce = await API.get('common/wish')
			console.log('WISH Responce', responce)
			if(responce.status === 200) {
				const info = {
					wish: responce.data,
					countWishProducts: responce.data.total_products?responce.data.total_products:0
				}

				dispatch(fetchWishInfoSuccess(info))
				callback()
				const end = new Date()
				console.log('WISH time = ', end - start)
			}
		} catch(e){
			console.log(e)
			dispatch(fetchWishInfoError(e))
		}
	}
}

export function deleteWishAll(callback = () => {}){
	return async dispatch => {
		try{
			const responce = await API.post('common/wish/remove_all')
			if(responce.status === 200) {
				dispatch(fetchWishInfo(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}

export function deleteWishProduct(product_id, callback = () => {}){
	return async dispatch => {
		let productData  = new URLSearchParams()
		productData.append('product_id', product_id)

		try{
			const responce = await API.post('common/wish/remove', productData)
			if(responce.status === 200) {
				dispatch(fetchWishInfo(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}

export function updateWish(product_id, quantity, callback = () => {}){
	return async dispatch => {
		let productData  = new URLSearchParams()
		productData.append('product_id', product_id)
		productData.append('total', quantity)

		try{
			const responce = await API.post('common/wish/update', productData)
			if(responce.status === 200) {
				dispatch(fetchWishInfo(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}

export function fetchWishInfoStart(){
	return {
		type: FETCH_WISH_INFO_START
	}
}
export function fetchWishInfoSuccess(info){
	return {
		type: FETCH_WISH_INFO_SUCCESS,
		wish: info.wish,
		countWishProducts: info.countWishProducts
	}
}
export function fetchWishInfoError(e){
	return {
		type: FETCH_WISH_INFO_ERROR,
		error: e
	}
}



