import React from 'react'
import {Route, withRouter, Switch, Redirect, useLocation} from "react-router-dom"
import {Container, Row, Col, Input} from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import Coupon from "../Coupon/Coupon";
import './CartInfo.scss'

function CartInfo(props){
    const text = (prop) => {
        if(props.language){
            return props.language[prop]
        }
    }

    const renderTotalsTop = () => {
        if(props.totals) {
            return Object.keys(props.totals).map((index) => {
                const total = props.totals[index]
                return (
                    <React.Fragment
                        key={'fr-cart-total-' + index}
                    >
                        {total['code'] == 'sub_total'
                            ? <div
                                key={'cart-total-' + index}
                                className="total-wrap"
                            >
                                <span className="total-title-sub"><b>{total.title}</b></span>
                                <span className="total-textt-sub">{total.text}</span>
                            </div>
                            : null
                        }
                        {total['code'] == 'tax'
                            ? <div
                                key={'cart-total-' + index}
                                className="total-wrap"
                            >
                                <span className="total-title-tax">{total.title}</span>
                                <span className="total-textt-tax">{total.text}</span>
                            </div>
                            : null
                        }
                    </React.Fragment>
                )
            })
        }
    }

    const renderPayment = () => {
        return (
            <div id="payment" className="text-left" style="display:none;">

            </div>
        )
    }

    const renderTerms = () => {
        return (
            <React.Fragment>
                { props.rules.display
                    ? <div className="terms">
                        <label>
                            {props.language.text_agree
                                ? <React.Fragment>
                                    <Input
                                        type="checkbox"
                                        name="agree"
                                        value="1"
                                        checked={ props.rules.default || !props.rules.display ? "checked" : false }
                                    />
                                    { props.language.text_agree }
                                </React.Fragment>
                                : null
                            }
                        </label>
                        <Button
                            type="button"
                            id="button-payment-method"
                            className="terms-btn btn btn-primary"
                            data-loading-text={ props.language.text_loading }
                            checked={ props.disable_button ? "checked" : false }
                        >
                            { props.language.button_buy_now }
                        </Button>
                    </div>
                    : null
                }
            </React.Fragment>
        )
    }

    const renderTotals = () => {
        if(props.totals) {
            return Object.keys(props.totals).map((index) => {
                const total = props.totals[index]
                return (
                    <div className="total-wrap" key={'fr-checkout-total-' + index} >
                        <span className="total-title-sub"><b>{total.title}</b></span>
                        <span className="total-textt-sub">{total.text}</span>
                    </div>
                )
            })
        }
    }

    const renderTotalsBottom = () => {
        if(props.totals) {
            return Object.keys(props.totals).map((index) => {
                const total = props.totals[index]
                return (
                    <React.Fragment
                        key={'fr-cart-total-wrap-' + index}
                    >
                        {total['code'] == 'coupon'
                            ? <div
                                key={'cart-total-wrap-' + index}
                                className="total-wrap"
                            >
                                <span className="total-title-coupon"><b>{total.title}</b></span>
                                <span className="total-textt--coupon">
                                    <span><b>{total.text.split(' ', 2)[0]} </b>
                                        <span className="currency">{total.text.split(' ', 2)[1]}</span>
                                    </span>
                                </span>
                            </div>
                            : null
                        }
                        {total['code'] == 'total'
                            ? <div
                                key={'cart-total-wrap-' + index}
                                className="total-wrap"
                            >
                                <span className="total-title-total"><b>{total.title}</b></span>
                                <span className="total-textt-total">
                                    <span><b>{total.text.split(' ', 2)[0]} </b>
                                        <span className="currency">{total.text.split(' ', 2)[1]}</span>
                                    </span>
                                </span>
                            </div>
                            : null
                        }
                    </React.Fragment>
                )
            })
        }
    }

    const renderModules = () => {
        return Object.keys(props.modules).map((index) => {
            const module = props.modules[index]
            if(module.coupon !== null) {
                return (
                    <Coupon
                        key={'cart-module-' + index}
                        language={props.language}
                        coupon={module.coupon}
                        couponError={props.couponError}
                        applyCoupon={props.applyCoupon}
                    />
                )
            }
        })
    }

    return(
        <div id="cart-info-table-wrap" className="main-theme-bg">
            <div id="cart-info-table">
                <div className={props.coupon?'coupon-success product-cart-info':'product-cart-info'}>
                    {/*<div className="total-top-wrap">*/}
                    {/*    {renderTotalsTop()}*/}
                    {/*</div>*/}
                    {props.modules
                        ? <div className="module-info-wrap">
                            {renderModules()}
                         </div>
                        : null
                        }
                    <div className="module-info-wrap">
                        {/*{renderTotalsBottom()}*/}
                        {renderTotals()}
                        <span
                            className="but theme-border-button"
                            onClick={ () => props.history.push('/checkout')}
                        >
                            {text('button_checkout')}
                        </span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(CartInfo)