import React, {Component, useState} from 'react'
import { ButtonDropdown, Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import "./Currency.scss"

function Currency(props) {

    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggle = () => setDropdownOpen(prevState => !prevState);
    const change = (code) =>{
        toggle()
        props.changeCurrency(code)
    }

    const currentCurrency = () => {
        if(props.currencies.currencies.length) {
            return props.currencies.currencies.map((currency, index) => {
                if (currency.code === props.currencies.code) {
                    return (
                        <React.Fragment key={'cc' + index}>
                            <span id={currency.code} className="lang-name">{currency.code}</span>
                            <span className="caret-down"> </span>
                        </React.Fragment>
                    )
                } else {
                    return null
                }
            })
        }
    }

    const menuCurrency = () => {
        if(props.currencies.currencies.length) {
            return props.currencies.currencies.map((currency, index) => {
                if (currency.code !== props.currencies.code) {
                    return (
                        <React.Fragment key={'cm' + index}>
                            {currency.code !== props.currencies.currencies.code
                                ? <li
                                    key={'curr' + index}
                                    onClick={() => {change(currency.code)}}
                                >
                                    <span className="currency-select">{currency.code}</span>
                                </li>
                                : null
                            }
                        </React.Fragment>
                    )
                } else {
                    return null
                }
            })
        }
    }

    return(
        <>
            { props.currencies.currencies.length > 1
                ? <Dropdown className="header-currency-dropdown" isOpen={dropdownOpen} toggle={toggle}>
                    <DropdownToggle>
                        { currentCurrency() }
                    </DropdownToggle>
                    <DropdownMenu>
                        { menuCurrency() }
                    </ DropdownMenu>
                </Dropdown>
                : null
            }
        </>
    )

}
export default Currency