import React, {useState} from 'react'
import classNames from "classnames";
import NotFound from "../../components/NotFound/NotFound";
import CheckoutLogin from "../../components/CheckoutLogin/CheckoutLogin"
import {Container, Row, Col, Collapse, Button, NavLink, ButtonGroup, Form} from 'reactstrap'
import './Checkout.scss'
import CheckoutInfo from "../../components/CheckoutInfo/CheckoutInfo"
import CheckoutGuest from "../../components/CheckoutGuest/CheckoutGuest"
import Coupon from "../../components/Coupon/Coupon"
import Login from "../Auth/Login";
import CustomerAddress from "../../components/CustomerAddress/CustomerAddress";
import ShippingMethod from "../../components/ShippingMethod/ShippingMethod"
import PaymentMethod from "../../components/PaymentMethod/PaymentMethod";

function Checkout(props) {

	//const loginModule = !props.auth.logged && props.checkout && props.checkout.login_module

	const openAllSections = () => {
		 if(!isOpenPaymentAddress) togglePaymentAddress()
		 if(!isOpenShippingAddress) toggleShippingAddress()
		 if(!isOpenShippingMethod) toggleShippingMethod()
		 if(!isOpenPaymentMethod) togglePaymentMethod()
		 if(!isOpenCartModule) toggleCartModule()
		 if(isOpenLogin) toggleLogin()
		return false
	}

	const [isOpenShippingAddressBlock, setIsOpenShippingAddressBlock] = useState(null)
	const setIsOpenShipping = (value) => { setIsOpenShippingAddressBlock(value)}
	const toggleShippingAddressBlock = () => setIsOpenShippingAddressBlock(!isOpenShippingAddressBlock)

	const [isOpenLogin, setIsOpenLogin] = useState(true)
	const toggleLogin = () => setIsOpenLogin(!isOpenLogin)

	const [isOpenPaymentAddress, setIsOpenPaymentAddress] = useState(false)
	const togglePaymentAddress = () => setIsOpenPaymentAddress(!isOpenPaymentAddress)

	const [isOpenShippingAddress, setIsOpenShippingAddress] = useState(false)
	const toggleShippingAddress = () => setIsOpenShippingAddress(!isOpenShippingAddress)

	const [isOpenShippingMethod, setIsOpenShippingMethod] = useState(false)
	const toggleShippingMethod = () => setIsOpenShippingMethod(!isOpenShippingMethod)

	const [isOpenPaymentMethod, setIsOpenPaymentMethod] = useState(false)
	const togglePaymentMethod = () => setIsOpenPaymentMethod(!isOpenPaymentMethod)

	const [isOpenCartModule, setIsOpenCartModule] = useState(false)
	const toggleCartModule = () => setIsOpenCartModule(!isOpenCartModule)

	let timeoutSave
	const saveData = (type) => {
		if (timeoutSave) {clearInterval(timeoutSave)}
		timeoutSave = setInterval(() => { saveFunction(type) }, 1000)
	}
	const saveFunction = (type) => {
		const addressForm = document.forms[type + 'AddressForm']
		const customerData = new FormData(addressForm)
		props.saveCustomerData(type, customerData)
		clearInterval(timeoutSave)
		return false
	}

	return(
		<React.Fragment>
			{ props.checkout && props.checkout.language
				? <React.Fragment>
					{!props.cart.text_empty
						? <Row>
							<Col lg={{size: 12, offset: 0}} xl={{size: 8, offset: 0}}>
								<div id="quickcheckoutconfirm" className={'main-theme-bg'}>
									{props.checkout.quickcheckout_layout == '4'
										? <div className="qc-col-0">
											{props.auth.loginInfo
												? <Collapse
													isOpen={isOpenLogin}
												  >
													<div className="qc-step" data-col={ props.checkout.step['login']['column'] }
														   data-row={ props.checkout.step['login']['row'] }>
														<div id="login-box">
															<div id="checkout">
																<div className="quickcheckout-content">
																	<CheckoutLogin
																		language={props.checkout.login.language}
																		info={props.auth.loginInfo}
																		register={props.register}
																		errorsAuth={props.errorsAuth}
																		messagesAuth={props.messagesAuth}
																		sendAuthData={props.sendAuthData}
																		openAllSections={openAllSections}
																	/>
																</div>
															</div>
														</div>
													</div>
												</Collapse>
												: <React.Fragment>
													{isOpenLogin
														? openAllSections()
														: null
													}
												</React.Fragment>
											}
											<div id="payment-address" className="panel qc-step"
												 data-col={props.checkout.step['payment_address']['column']}
												 data-row={props.checkout.step['payment_address']['row']}
											>
												<Form name={'paymentAddressForm'}>
													<div
														onClick={togglePaymentAddress}
														className={classNames('payment-address-heading quickcheckout-heading', {open: isOpenPaymentAddress})}
													>
														{!props.checkout.logged
															? props.checkout.language.text_checkout_account
															: props.checkout.language.text_checkout_payment_address
														}
														<span className="arrow-down"></span>
													</div>
													<Collapse
														isOpen={isOpenPaymentAddress}
														className="panel-collapse"
													>
														<div className="quickcheckout-content">
															{props.checkout.guest
																? <React.Fragment>
																	{isOpenShippingAddressBlock === null && !props.checkout.guest.shipping_address ? setIsOpenShipping(true) : null}
																	<CheckoutGuest
																		form={props.checkout.guest}
																		countries={props.countries}
																		customerGroups={props.customerGroups}
																		language={props.checkout.language}
																		saveData={saveData}
																		toggleShippingAddressBlock={toggleShippingAddressBlock}
																		isOpenShippingAddressBlock={isOpenShippingAddressBlock}
																		selectColourStyles={props.selectColourStyles}
																		fetchShippingMethod={props.fetchShippingMethod}
																		fetchPaymentMethod={props.fetchPaymentMethod}
																		error={props.errorPayment}
																		errorRegister={props.errorRegister}

																	/>
																</React.Fragment>
																: <React.Fragment>
																	{isOpenShippingAddressBlock === null ? setIsOpenShipping(true) : null}
																	<CustomerAddress
																		addressType={'payment'}
																		addresses={props.checkout.addresses}
																		countries={props.countries}
																		customerGroups={props.customerGroups}
																		language={props.checkout.language}
																		saveData={saveData}
																		setIsOpenShipping={setIsOpenShipping}
																		form={props.checkout.payment_address}
																		selectColourStyles={props.selectColourStyles}
																		fetchPaymentMethod={props.fetchPaymentMethod}
																		error={props.errorPayment}
																	/>
																</React.Fragment>
															}
														</div>
													</Collapse>
												</Form>
											</div>
											{props.checkout.shipping_required
												? <Collapse
													isOpen={isOpenShippingAddressBlock}
												>
													<Form name={'shippingAddressForm'}>
														<div id="shipping-address"
															   className={classNames('panel qc-step', {hidden: !props.checkout.show_shipping_address})}
															   data-col={ props.checkout.step['shipping_address']['column'] }
															   data-row={ props.checkout.step['shipping_address']['row'] }
														>
															<div
																onClick={toggleShippingAddress}
																className={classNames('shipping-address-heading quickcheckout-heading', {open: isOpenShippingAddress})}
															>
																{props.checkout.language.text_checkout_shipping_address}
																<span className="arrow-down"></span>
															</div>
															<Collapse
																isOpen={isOpenShippingAddress}
																className="panel-collapse"
															>
																<div className="quickcheckout-content">
																	{props.checkout.shipping_address || props.checkout.guest_shipping
																		? <CustomerAddress
																			guest={!!props.checkout.guest_shipping}
																			addressType={'shipping'}
																			addresses={props.checkout.addresses}
																			countries={props.countries}
																			customerGroups={props.customerGroups}
																			language={props.checkout.language}
																			saveData={saveData}
																			form={props.checkout.shipping_address ? props.checkout.shipping_address : props.checkout.guest_shipping}
																			selectColourStyles={props.selectColourStyles}
																			fetchPaymentMethod={props.fetchPaymentMethod}
																			fetchShippingMethod={props.fetchShippingMethod}
																			error={props.errorShipping}
																		/>
																		: null
																	}
																</div>
															</Collapse>
														</div>
													</Form>
												</Collapse>
												: null
											}
											{props.checkout.shipping_required && props.shippingMethod
												? <div id="shipping-method"
													   className={classNames('panel qc-step', {hidden: !props.checkout.shipping_module})}
													   data-col={ props.checkout.step['shipping_method']['column'] }
													   data-row={ props.checkout.step['shipping_method']['row'] }>
													<div
														onClick={toggleShippingMethod}
														className={classNames('shipping-method-heading quickcheckout-heading', {open: isOpenShippingMethod})}
													>
														{props.checkout.language.text_checkout_shipping_method}
														<span className="arrow-down"></span>
													</div>
													<Form name={'shippingMethodForm'}>
														<Collapse
															isOpen={isOpenShippingMethod}
															className="panel-collapse"
														>
															<div className="quickcheckout-content">
																<ShippingMethod
																	form={props.shippingMethod}
																	session={props.session}
																	//totals={props.totals}
																	setShippingMethod={props.setShippingMethod}
																	selectColourStyles={props.selectColourStyles}
																/>
															</div>
														</Collapse>
													</Form>
												</div>
												: null
											}
											<Form name={'paymentMethodForm'}>
											{(props.checkout.survey_module && !props.checkout.payment_module) || (props.checkout.field_comment.display && !props.checkout.payment_module)
												? <div id="payment-method" className="panel qc-step"
													   data-col={ props.checkout.step['payment_method']['column'] }
													   data-row={ props.checkout.step['payment_method']['row'] }>
													<div
														onClick={togglePaymentMethod}
														className={classNames('payment-method-heading quickcheckout-heading', {open: isOpenPaymentMethod})}
													>
														{props.checkout.language.text_comment}
														<span className="arrow-down"></span>
													</div>
													<Collapse
														isOpen={isOpenPaymentMethod}
														className="panel-collapse"
													>
														<div className="quickcheckout-content">

														</div>
													</Collapse>
												</div>
												: <React.Fragment>
													{ props.paymentMethod
														? <div id="payment-method"
															   className={classNames('panel qc-step', {hidden: !props.checkout.payment_module})}
															   data-col={props.checkout.step['payment_method']['column']}
															   data-row={props.checkout.step['payment_method']['row']}
														>
															<div
																onClick={togglePaymentMethod}
																className={classNames('payment-method-heading quickcheckout-heading', {open: isOpenPaymentMethod})}
															>
																{props.checkout.language.text_checkout_payment_method}
																<span className="arrow-down"></span>
															</div>
															<Collapse
																isOpen={isOpenPaymentMethod}
																className="panel-collapse"
															>
																<div className="quickcheckout-content">
																	<PaymentMethod
																		form={props.paymentMethod}
																		session={props.session}
																		setPaymentMethod={props.setPaymentMethod}
																		selectColourStyles={props.selectColourStyles}
																	/>
																</div>
															</Collapse>
														</div>
														: null
													}
												</React.Fragment>
											}
											</Form>
											{parseInt(props.checkout.cart_module)
												? <div className="qc-step"
													   data-col={ props.checkout.step['cart']['column'] }
													   data-row={ props.checkout.step['cart']['row'] }>

													<div id="cart1" className="panel">
														<div
															onClick={toggleCartModule}
															className={classNames('cart1-heading quickcheckout-heading', {open: isOpenCartModule})}
														>
															{props.checkout.language.text_cart}
															<span className="arrow-down"></span>
														</div>
														<Collapse
															isOpen={isOpenCartModule}
															className="panel-collapse"
														>
															<div className="quickcheckout-content"
																 style={{border: 'none', padding: '0'}}
															></div>
														</Collapse>
													</div>
												</div>
												: null
											}
											{props.checkout.coupon_module  ? null : null}
											{props.checkout.reward_module ? null : null}
											{props.checkout.voucher_module ? null : null}
										</div>
										: null
									}
								</div>
							</Col>
							<Col xs={{size: 12, offset: 0}} sm={{size: 8, offset: 2}} lg={{size: 6, offset: 3}} xl={{size: 4, offset: 0}}>
								<CheckoutInfo
									cart={props.cart}
									terms={props.terms}
									checkout={props.checkout}
									language={props.checkout.language}
									totals={props.totals}
									vouchers={props.checkout.vouchers}
									modules={props.checkout.modules}
									couponError={props.couponError}
									applyCoupon={props.applyCoupon}
									validateCustomerData={props.validateCustomerData}
									errorPayment={props.errorPayment}
									errorPaymentMethod={props.errorPaymentMethod}
									errorShipping={props.errorShipping}
									errorShippingMethod={props.errorShippingMethod}
									errorRegister={props.errorRegister}
									errorTerms={props.errorTerms}
									orderConfirm={props.orderConfirm}
									fetchSuccessInfo={props.fetchSuccessInfo}
									orderInfo={props.orderInfo}
								/>
							</Col>
						</Row>
						: <NotFound
							language={props.cart.language}
						  />
					}
				</React.Fragment>
				: null
			}
		</React.Fragment>
	)
}

export default Checkout
