import React, {useState} from 'react'
import {
    Container,
    Row,
    Col,
    Button,
    NavLink,
    ButtonGroup,
    Tooltip,
    CustomInput,
    CustomInputGroup,
    Form,
    FormGroup, CardFooter, CardBody, Card, Label, Input, Collapse
} from 'reactstrap'
import Select from "react-select"
import './CustomerAddress.scss'
import CustomerForm from "../CustomerForm/CustomerForm";
import Message from "../ui/message/Message";
import classNames from "classnames";
import ReactHtmlParser from "react-html-parser";

function CustomerAddress(props){
// console.log('CustomerAddress(props)', props)
// console.log('customerAddress', props.addresses && Object.keys(props.addresses).length)
    const [customerAddress, setCustomerAddress] = useState(() => {return (props.addresses && Object.keys(props.addresses).length)})
    const shipping = !!(props.form.show_shipping_address && props.form.shipping_checkbox.default)
//console.log('[customerAddress]', props.addresses && Object.keys(props.addresses).length)

    //const [shipping, setShipping] = useState(() => {return !!(props.form.show_shipping_address && props.form.shipping_checkbox.default)})
    // const setShippingCheck = () => {
    //     props.setIsOpenShipping(shipping)
    //     setShipping(!shipping)
    //     props.saveData(props.addressType)
    // }

    const [currentAddress, setCurrentAddress] = useState(() => { return props.form.address_id})

    const onSelectChange = (value)=> {
        setCurrentAddress(value.dataValue)
        props.saveData(props.addressType)
        return null
    }

    // const [methods, setMethods] = useState(false)
    // const fetchMethodByAddressId = (addressId) =>{
    //     console.log(addressId)
    //     if (props.fetchShippingMethod){
    //         props.fetchShippingMethod(null, addressId)
    //     } else if(props.fetchPaymentMethod){
    //         props.fetchPaymentMethod(null, addressId)
    //     }
    //     setMethods(true)
    // }

    const addressSelect = () => {
        if(props.addresses) {
            let optionsArray = []
            Object.keys(props.addresses).map((index) => {
                const option = props.addresses[index]
                const label = option.firstname + ' ' + option.lastname + ', ' + option.address_1 + ', ' + option.city + (option.postcode?(', ' + option.postcode):'') + ', ' + option.zone + ', ' + option.country
                optionsArray.push({ dataValue: option.address_id, label: label })
            })
            let index = null

            if(optionsArray.length) {
                index = optionsArray.findIndex(item => item['dataValue'] === currentAddress)
            }
            const valueKey = (index && currentAddress && index + 1) ? index : 0
            const currentAddressId = optionsArray[valueKey].dataValue
            //if(!methods){ fetchMethodByAddressId(currentAddressId)}

            return (
                <FormGroup>
                    <Select
                        className='react-select-container'
                        classNamePrefix="react-select"
                        options={optionsArray}
                        defaultValue={optionsArray[valueKey]}
                        styles={props.selectColourStyles}
                        onChange={onSelectChange.bind(this)}
                    />
                    <Input
                        type="hidden"
                        name={'address_id'}
                        id={props.addressType + '_address_id'}
                        value={parseInt(currentAddress)?currentAddress:currentAddressId}
                        readOnly={true}
                    />
                </FormGroup>
            )
        } else {
            return null
        }
    }

    return (
        <Container fluid={true}>
            <Row>
                <Col sm={'12'}>
                    {props.addresses && Object.keys(props.addresses).length
                        ? <React.Fragment>
                            <div className="radio">
                                <CustomInput
                                    type="radio"
                                    defaultChecked={customerAddress}
                                    value={'existing'}
                                    id={props.addressType + "-address-existing"}
                                    name={props.addressType + "_address"}
                                    label={props.language.text_address_existing}
                                    onChange={() => {
                                        setCustomerAddress(!customerAddress)
                                        props.saveData(props.addressType)
                                        }}
                                />
                            </div>
                            <div id={props.addressType + "-existing"}>
                                {addressSelect()}
                            </div>
                            <div className="radio">
                                <CustomInput
                                    type="radio"
                                    defaultChecked={!customerAddress}
                                    value={'new'}
                                    id={props.addressType + "-address-new"}
                                    name={props.addressType + "_address"}
                                    label={props.language.text_address_new}
                                    onChange={() => {
                                        setCustomerAddress(!customerAddress)
                                        props.saveData(props.addressType)
                                        console.log('CustomerAddres-128')
                                    }}
                                />
                            </div>
                        </React.Fragment>
                        :  <Input
                                type="hidden"
                                value={'new'}
                                name={props.addressType + "_address"}
                                readOnly={true}
                           />
                    }
                </Col>
            </Row>
            <Collapse
                isOpen={(props.guest && !shipping) || (!customerAddress && !props.guest)}
                className="panel-collapse"
            >
                {/*{console.log('props.guest', props.guest)}*/}
                {/*{console.log('!shipping', !shipping)}*/}
                {/*{console.log('[customerAddress]', customerAddress)}*/}
                {/*{console.log('!customerAddress', !customerAddress)}*/}
                {/*{console.log('!props.guest', !props.guest)}*/}
                <CustomerForm
                    addressType={props.addressType}
                    form={props.form}
                    countries={props.countries}
                    saveData={props.saveData}
                    selectColourStyles={props.selectColourStyles}
                    language={props.language}
                    fetchShippingMethod={props.fetchShippingMethod}
                    fetchPaymentMethod={props.fetchPaymentMethod}
                    error={props.error}
                />
            </Collapse>
        </Container>
    )
}

export default CustomerAddress