import React, {Component} from 'react'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './CheckoutSuccess.scss'
import ReactHtmlParser from "react-html-parser";
import NotFound from "../NotFound/NotFound";
function CheckoutSuccess(props){


	return (
		<React.Fragment>
			{props.success.text_message
				? <div id={'site-success'} className="maintenance-content main-theme-bg">
					<Container>
					{ReactHtmlParser(props.success.text_message)}
					{props.success.order_info
					? <div className={'order-info-wrap'}>
						<span className="order-info-row">
							<span className="order-info-text">{props.success.text_order}</span>
							<span className="order-info-value">{props.success.order_info.order_id}</span>
						</span>
						<span className="order-info-row">
							<span className="order-info-text">{props.success.text_shipping}</span>
							<span className="order-info-value">{props.success.order_info.shipping_method}</span>
						</span>
						<span className="order-info-row">
							<span className="order-info-text">{props.success.text_shipping_adress}</span>
							<span className="order-info-value">
								{props.success.order_info.shipping_country},
								{props.success.order_info.shipping_address_1}
								{props.success.order_info.shipping_address_2? ', ' + props.success.order_info.shipping_address_2: null}
							</span>
						</span>
						<span className="order-info-row">
							<span className="order-info-text">{props.success.text_receiver}</span>
							<span className="order-info-value">
								{props.success.order_info.shipping_firstname} {props.success.order_info.shipping_lastname}
								{props.success.order_info.telephone? ', ' + props.success.order_info.telephone: null}
								, {props.success.order_info.email}
							</span>
						</span>
						<span className="order-info-row">
							<span className="order-info-text">{props.success.text_payment_method}</span>
							<span className="order-info-value">{props.success.order_info.payment_method}</span>
						</span>
						<span className="order-info-row">
							<span className="order-info-text">{props.success.text_total_payment}</span>
							<span className="order-info-value">{Math.round(parseFloat(props.success.order_info.total)*100)/100}{props.success.currency_symbol}</span>
						</span>
					  </div>
					: null
					}
					<div className={'buttons'}>
						<NavLink href={'/'}>
							<Button className="theme-border-button" type="button" color="none">
								<span>{props.language.button_shopping}</span>
							</Button>
						</NavLink>
					</div>
				</Container>
				</div>
				: <NotFound
					language={props.language}
				  />
			}
		</React.Fragment>
	)
}

export default CheckoutSuccess
