import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './Rewards.scss'
import {RiLoginBoxLine, RiUserAddLine, RiLockPasswordLine, RiLogoutBoxRLine} from 'react-icons/ri'
import {FaRegUserCircle} from 'react-icons/fa'
import {GiAlliedStar} from 'react-icons/gi'
function Rewards(props){

	const text = (prop) => {
		if(props.accountRewards && props.accountRewards.language){
			return ReactHtmlParser(props.accountRewards.language[prop])
		}
	}

	return (
		<div
			className="account-rewards main-theme-bg"
			onClick={() => { props.history.push('/rewards') }}
		>
			{console.log(props)}
			{props.accountRewards
				? <React.Fragment>
					<div className="profile-title-wrap">
						<h3 className={'profile-title'}>
							<GiAlliedStar style={{marginBottom: '6px', marginRight: '7px'}}/>
							{props.accountRewards.heading_title}
						</h3>
					</div>
					<div className="rewards-data-wrap">
						<div className="rewards-data">
							<span className={'data-name'}>{text('text_total')}</span>
							<span className={'data-value'}>{props.accountRewards.reward_total}</span>
						</div>
					{/*	<div className="profile-data">*/}
					{/*		<span className={'data-name'}>{text('entry_lastname')}</span>*/}
					{/*		<span className={'data-value'}>{props.accountRewards.lastname}</span>*/}
					{/*	</div>*/}
					</div>
				</React.Fragment>
				: null
			}
		</div>
	)
}

export default withRouter(Rewards)
