import React, {Component, useState} from 'react'
import {Route, withRouter, Switch, Redirect, useLocation} from "react-router-dom";
import AuthNav from "components/Navbars/AuthNavbar.js";
import Footer from "components/Footer/Footer.js";
import { BackgroundColorContext } from "contexts/BackgroundColorContext"
import Header from "../../components/Header/Header"
import {connect} from "react-redux"
import Login from "../Auth/Login"
import Register from "../Auth/Register"
import Forgotten from "../Auth/Forgotten"
import Cart from "../Cart/Cart"
import CartTable from "../../components/CartTable/CartTable"
import Account from "../Account/Account";
import Profile from "../../components/Profile/Profile";
import Addresses from "../../components/Addresses/Addresses";
import Orders from "../../components/Orders/Orders";
import Rewards from "../../components/Rewards/Rewards";
import routes from "../../routes";
import {fetchInitInfo, changeCurrency, changeLanguage, sendAuthData, logout} from "../../store/actions/main"
import {fetchCartInfo, updateCart, deleteCartProduct, applyCoupon, wishToCart,
  fetchPaymentMethod, setPaymentMethod,
  fetchShippingMethod, setShippingMethod,
  saveCustomerData, fetchCustomerOptions,
  validateCustomerData, orderConfirm, fetchSuccessInfo
} from "../../store/actions/cart"
import {fetchWishInfo, updateWish, deleteWishProduct, deleteWishAll} from "../../store/actions/wish"
import {fetchAccountInfo,sendAccountData} from "../../store/actions/account"
import API from "../../utils/API";
import App from "../../App";
import Logout from "../../components/Logout/Logout";
import Checkout from "../Checkout/Checkout";
import CheckoutSuccess from "../../components/CheckoutSuccess/CheckoutSuccess";

class MainLayout extends Component {

  state = {
    sidebarOpened: false,
    //activeTab: 'login'
    selectColourStyles : {
      control: styles => ({ ...styles, backgroundColor: '#26272a' }),
      option: (styles, { isDisabled, isFocused, isSelected }) => {
        return {
          ...styles,
          backgroundColor: isDisabled
              ? null
              : isSelected
                  ? '#26272a'
                  : isFocused
                      ? 'blue'
                      : null,
          color: isDisabled
              ? '#ccc'
              : isSelected
                  ? 'black'
                  : 'green',
          cursor: isDisabled ? 'not-allowed' : 'pointer',

          ':active': {
            ...styles[':active'],
            backgroundColor:
                !isDisabled && (isSelected ? 'red' : 'blue'),
          }
        }
      }
    }
  }

  changeLanguage = (code) => {
    this.props.changeLanguage(code)
    return true
  }
  changeCurrency = (code) => {
    this.props.changeCurrency(code)
    return true
  }
  logout = () => {
    this.props.logout()
    return null
  }

  // register = () =>{
  //   this.props.history.push('/register')
  // }

  title = () => {
    //const path = props.pathname.split('/').pop()
    const path = this.props.location.pathname.split('/')[1]
    if(path && this.props.auth[path + 'Info'] && this.props.auth[path + 'Info'].heading_title){
      return this.props.auth[path + 'Info'].heading_title
    } else if (path && this.props[path] && this.props[path].heading_title){
      return this.props[path].heading_title
    }
    return null
  }

  UNSAFE_componentWillMount(){
    if(!this.props.main.header.length){
      this.props.fetchInitInfo()
      this.props.fetchCustomerOptions()
    }
  }

  render() {
    return (
        <BackgroundColorContext.Consumer>
          {({color, changeColor}) => (
              <React.Fragment>
                <Header
                    header = { this.props.main.header }
                    pathname={this.props.location?this.props.location.pathname:'/'}
                    auth={this.props.auth}
                    changeLanguage = {this.changeLanguage}
                    changeCurrency = {this.changeCurrency}
                    countCartProducts = {this.props.countCartProducts}
                    countWishProducts = {this.props.countWishProducts}
                />
                <div className="container" style={{zIndex: 0}}>
                  <div className="category-top-panel-wrap main-theme-bg">
                    <AuthNav
                        logged={this.props.auth.logged}
                        logout={this.logout}
                        data={color}
                        pathname={this.props.location?this.props.location.pathname:'/'}
                        info={this.props.auth}
                        title={this.title()}
                        toggleSidebar={this.toggleSidebar}
                        sidebarOpened={this.state.sidebarOpened}
                    />
                  </div>
                </div>
                <div className="content-columns">
                  <div className="container">
                    <div id="content" className="main-column">
                      <Switch>
                        <Route path="/cart" render={() =>
                            <Cart
                                cart = {this.props.cart}
                                //checkout = {this.props.checkout}
                                totals={this.props.totals}
                                updateCart = {this.props.updateCart}
                                deleteCartProduct = {this.props.deleteCartProduct}
                                couponError = {this.props.couponError}
                                applyCoupon = {this.props.applyCoupon}
                                fetchCheckoutInfo = {this.props.fetchCheckoutInfo}
                            />
                        } />
                        <Route path="/checkout" render={() =>
                            <Checkout
                                checkout={this.props.checkout}
                                cart={this.props.cart}
                                terms={this.props.terms}
                                totals={this.props.totals}
                                session={this.props.session}
                                shippingMethod={this.props.shippingMethod}
                                paymentMethod={this.props.paymentMethod}
                                countries={this.props.countries}
                                customerGroups={this.props.customerGroups}
                                updateCart = {this.props.updateCart}
                                deleteCartProduct = {this.props.deleteCartProduct}
                                couponError = {this.props.couponError}
                                applyCoupon = {this.props.applyCoupon}
                                auth={this.props.auth}
                                errorsAuth={this.props.errorsAuth}
                                messagesAuth={this.props.messagesAuth}
                                sendAuthData={this.props.sendAuthData}
                                saveCustomerData={this.props.saveCustomerData}
                                selectColourStyles={this.state.selectColourStyles}
                                fetchShippingMethod={this.props.fetchShippingMethod}
                                fetchPaymentMethod={this.props.fetchPaymentMethod}
                                setShippingMethod={this.props.setShippingMethod}
                                setPaymentMethod={this.props.setPaymentMethod}
                                validateCustomerData={this.props.validateCustomerData}
                                errorPayment={this.props.errorPayment}
                                errorPaymentMethod={this.props.errorPaymentMethod}
                                errorShipping={this.props.errorShipping}
                                errorShippingMethod={this.props.errorShippingMethod}
                                errorRegister={this.props.errorRegister}
                                errorTerms={this.props.errorTerms}
                                orderConfirm={this.props.orderConfirm}
                                fetchSuccessInfo={this.props.fetchSuccessInfo}
                                orderInfo={this.props.orderInfo}
                            />
                        } />
                        <Route path="/success" render={() =>
                            <CheckoutSuccess
                                success={this.props.success}
                                language={this.props.main.language}
                            />
                         } />
                        <Route path="/wish" render={() =>
                            <CartTable
                                wish={true}
                                products={this.props.wish.products}
                                totalSum={this.props.wish.total_total}
                                language={this.props.wish.language}
                                deleteWishProduct={this.props.deleteWishProduct}
                                deleteWishAll={this.props.deleteWishAll}
                                updateWish = {this.props.updateWish}
                                wishToCart={this.props.wishToCart}
                            />
                         } />
                        <Route path="/logout" render={() =>
                            <Logout
                                info={this.props.auth.logoutInfo}
                                logged={this.props.auth.logged}
                                logout={this.logout}
                            />
                        }/>
                        {!this.props.auth.logged
                            ? <Redirect from={'/account'} to={'/login'}/>
                            : <Redirect from={'/login'} to={'/account'}/>
                        }
                         <React.Fragment>
                              { !this.props.auth.logged
                                  ? <React.Fragment>
                                    <Route path="/login" render={() =>
                                        <Login
                                            info={this.props.auth.loginInfo}
                                            errorsAuth={this.props.errorsAuth}
                                            messagesAuth={this.props.messagesAuth}
                                            sendAuthData={this.props.sendAuthData}
                                        />
                                    }/>
                                    <Route path="/register" render={() =>
                                        <Register
                                            info={this.props.auth.registerInfo}
                                            errorsAuth={this.props.errorsAuth}
                                            messagesAuth={this.props.messagesAuth}
                                            sendAuthData={this.props.sendAuthData}
                                        />
                                    }/>
                                    <Route path="/forgotten" render={() =>
                                        <Forgotten
                                            info={this.props.auth.forgottenInfo}
                                            errorsAuth={this.props.errorsAuth}
                                            messagesAuth={this.props.messagesAuth}
                                            sendAuthData={this.props.sendAuthData}
                                        />
                                    }/>
                                  </React.Fragment>
                                  : <React.Fragment>
                                        <Route path="/account" render={() =>
                                            <Account
                                                account={this.props.account}
                                                accountEdit={this.props.profile}
                                                accountOrders={this.props.orders}
                                                accountRewards={this.props.rewards}
                                                accountAddresses={this.props.addresses}
                                                accountCart={this.props.cart}
                                                accountWish={this.props.wish}
                                                language={this.props.account.language}
                                                languageMain={this.props.main.language}
                                                routes={this.props.account.routes}
                                            />
                                        }/>
                                        <Route path="/profile" render={() =>
                                            <Profile
                                                accountEdit={this.props.profile}
                                                language={this.props.account.language}
                                                languageMain={this.props.main.language}
                                            />
                                        }/>
                                        <Route path="/addresses" render={() =>
                                            <Addresses
                                                accountAddresses={this.props.addresses}
                                                language={this.props.account.language}
                                                languageMain={this.props.main.language}
                                            />
                                        }/>
                                        <Route path="/orders" render={() =>
                                            <Orders
                                                accountOrders={this.props.orders}
                                                language={this.props.account.language}
                                                languageMain={this.props.main.language}
                                            />
                                        }/>
                                        <Route path="/rewards" render={() =>
                                            <Rewards
                                                accountRewards={this.props.rewards}
                                                language={this.props.account.language}
                                                languageMain={this.props.main.language}
                                            />
                                        }/>
                                  </React.Fragment>
                              }
                         </React.Fragment>
                       </Switch>
                    </div>
                  </div>
                </div>
                <Footer
                  footer = { this.props.main.footer }
                />
              </React.Fragment>
          )}
        </BackgroundColorContext.Consumer>
    )
  }
}

function mapStateToProps(state){
  //console.log('MAIN STATE', state)
  //console.log('MAIN STATE SESSION', state.session.session)
  return {
    accountRoutes: state.main.routes,
    loading: state.main.loading,
    error: state.main.error,
    main: state.main.main,
    errorsAuth: state.main.errorsAuth,
    messagesAuth: state.main.messagesAuth,
    auth: state.main.auth,
    cart: state.cart.cart,
    success: state.cart.success,
    totals: state.cart.totals,
    terms: state.cart.terms,
    orderInfo: state.cart.orderInfo,
    errorTerms: state.cart.errorTerms,
    errorPayment: state.cart.errorPayment,
    errorPaymentMethod: state.cart.errorPaymentMethod,
    errorShipping: state.cart.errorShipping,
    errorShippingMethod: state.cart.errorShippingMethod,
    errorRegister: state.cart.errorRegister,
    checkout: state.cart.checkout,
    shippingMethod: state.cart.shippingMethod,
    paymentMethod: state.cart.paymentMethod,
    countries: state.cart.countries,
    customerGroups: state.cart.customerGroups,
    wish: state.wish.wish,
    account: state.account.account,
    profile: state.account.accountEdit,
    orders: state.account.accountOrders,
    rewards: state.account.accountRewards,
    addresses: state.account.accountAddresses,
    couponError: state.cart.couponError,
    countCartProducts: state.cart.countCartProducts,
    countWishProducts: state.wish.countWishProducts,
    session: state.session.session
  }
}

function mapDispatchToProps(dispatch){
  return {
    fetchInitInfo: () => dispatch(fetchInitInfo()),
    changeCurrency: (code) => dispatch(changeCurrency(code)),
    changeLanguage: (code) => dispatch(changeLanguage(code)),
    logout: (callback) => dispatch(logout(callback)),
    //fetchCartInfo: () => dispatch(fetchCartInfo()),
    //fetchCheckoutInfo: (callback) => dispatch(fetchCheckoutInfo(callback)),
    fetchCustomerOptions: () => dispatch(fetchCustomerOptions()),
    fetchShippingMethod: (shippingData, paymentAddressId) => dispatch(fetchShippingMethod(shippingData, paymentAddressId)),
    setShippingMethod: (shippingData, shippingAddressId) => dispatch(setShippingMethod(shippingData, shippingAddressId)),
    fetchPaymentMethod: (paymentData, paymentAddressId) => dispatch(fetchPaymentMethod(paymentData, paymentAddressId)),
    setPaymentMethod: (paymentData, paymentAddressId) => dispatch(setPaymentMethod(paymentData, paymentAddressId)),
    saveCustomerData: (type, customerData) => dispatch(saveCustomerData(type, customerData)),
    validateCustomerData: (type, customerData, callback) => dispatch(validateCustomerData(type, customerData, callback)),
    orderConfirm: () => dispatch(orderConfirm()),
    fetchSuccessInfo: (callback) => dispatch(fetchSuccessInfo(callback)),

    sendAuthData: (currentOption, authData) => dispatch(sendAuthData(currentOption, authData)),
    updateCart: (cartId, quantity, callback) => dispatch(updateCart(cartId, quantity, callback)),
    deleteCartProduct: (cartId, callback) => dispatch(deleteCartProduct(cartId, callback)),
    updateWish: (productId, quantity, callback) => dispatch(updateWish(productId, quantity, callback)),
    wishToCart: (productId, quantity, callback) => dispatch(wishToCart(productId, quantity, callback)),
    deleteWishProduct: (productId, callback) => dispatch(deleteWishProduct(productId, callback)),
    deleteWishAll: (callback) => dispatch(deleteWishAll(callback)),
    applyCoupon: (couponData) => dispatch(applyCoupon(couponData)),
    fetchAccountInfo: () => dispatch(fetchAccountInfo()),
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MainLayout))
