export function fetchCartInfo(callback = () => {}){
	const start = new Date()
	return async dispatch => {
		dispatch(fetchCartInfoStart())

		try{
			const response = await API.get('checkout/cart')
			console.log('CART response', response)
			if(response.status === 200 && response.data) {
				const info = {
					cart: response.data.cart?response.data.cart:null,
					countCartProducts: response.data.cart?response.data.cart.count_products:0,
					checkout: response.data.checkout?response.data.checkout: null
				}
				if(response.data.checkout){

				}
				dispatch(fetchTerms())
				dispatch(fetchTotals())
				dispatch(fetchCartInfoSuccess(info))
				const end = new Date()
				//console.log('CART time = ', end - start)
			}
		} catch(e){
			console.log(e)
			dispatch(fetchCartInfoError(e))
		}
	}
}

export function fetchCustomerOptions(){
	const start = new Date()
	return async dispatch => {
		dispatch(fetchCustomerOptionsStart())

		try{
			const response = await API.get('checkout/checkout/customerOptions')
			console.log('CUSTOMER OPTIONS response', response)
			if(response.status === 200) {
				let countries = []
				let customerGroups = []
				response.data.countries.map((country) => {
					countries.push({ dataValue: country.country_id, label: country.name })
				})
				response.data.customer_groups.map((group) => {
					customerGroups.push({ dataValue: group.coustomer_group_id, label: group.name })
				})
				const info = {
					countries: countries,
					customerGroups: customerGroups
				}

				dispatch(fetchCustomerOptionsSuccess(info))
				const end = new Date()
				//console.log('CUSTOMER OPTIONS time = ', end - start)
			}
		} catch(e){
			console.log(e)
			dispatch(fetchCustomerOptionsError(e))
		}
	}
}

