export function validateCustomerData(type, customerData, callback = () => {}){
	const start = new Date()
	return async dispatch => {
		dispatch(validateCustomerDataStart())
			try{
				const response = await API.post('checkout/' + type + '/validate', customerData)
				console.log('VALIDATE ' + type + ' response', response)
				let info = {}
				if(response.status === 200) {
					if(response.data && (type === 'guest' || type === 'payment_address')) {
						info = {error: response.data.error}
						dispatch(validatePaymentDataSuccess(info))
					} else if(response.data && (type === 'guest_shipping' || type === 'shipping_address')){
						info = {error: response.data.error}
						dispatch(validateShippingDataSuccess(info))
					} else if(type === 'register'){
						info = {error: response.data?response.data.error:null}
						dispatch(validateRegisterDataSuccess(info))

					} else if(response.data && type === 'payment_method'){
						info = {error: response.data.error}
						//dispatch(fetchTotals())
						dispatch(validatePaymentMethodDataSuccess(info))
					} else if(response.data && type === 'shipping_method'){
						info = {error: response.data.error}
						//dispatch(fetchTotals())
						dispatch(validateShippingMethodDataSuccess(info))
					} else if(response.data && type === 'terms'){
						info = {error: response.data.error}
						dispatch(validateTermsDataSuccess(info))
					}

					if(response.data && response.data.session) {
						dispatch(fetchSession(response.data.session))
						//dispatch(fetchTotals())
					}

					callback(!info.error)

					const end = new Date()
					//console.log('VALIDATE CustomerData time = ', end - start)
					console.log(!info.error)
				}
			} catch(e){
				console.log(e)
				dispatch(validateCustomerDataError(e))
			}

	}
}

