export function fetchShippingMethod(shippingData = null, addressId = null){
	const start = new Date()

	return async dispatch => {
		dispatch(fetchShippingMethodStart())
		const address =  addressId?'&address_id=' + addressId:''
		try{
			const response = await API.post('checkout/shipping_method' + address, shippingData)
			console.log('SHIPPING METHOD response', response)
			if(response.status === 200) {
				const info = {
					shippingMethod: response.data,
				}

				if(response.data && response.data.session) {
					dispatch(fetchSession(response.data.session, response.data.code))
				}

				dispatch(fetchShippingMethodSuccess(info))
				const end = new Date()
				//console.log('SHIPPING METHOD time = ', end - start)
			}
		} catch(e){
			console.log(e)
			dispatch(fetchShippingMethodError(e))
		}
	}
}

export function setShippingMethod(shippingData = null, addressId = null) {
	const start = new Date()

	return async dispatch => {
		dispatch(setShippingMethodStart())
		const address =  addressId?'&address_id=' + addressId:''
		try{
			const response = await API.post('checkout/shipping_method/set' + address, shippingData)
			console.log('SET SHIPPING METHOD response', response)
			if(response.status === 200) {

				if(response.data && response.data.session) {
					dispatch(fetchSession(response.data.session))
				}

				dispatch(fetchTotals())

				const end = new Date()
				//console.log('SET SHIPPING METHOD time = ', end - start)
			}
		} catch(e){
			console.log(e)
			dispatch(setShippingMethodError(e))
		}
	}
}

