export function deleteCartProduct(cart_id, callback = () => {}){
	return async dispatch => {

		let productData  = new URLSearchParams()
		productData.append('key', cart_id)

		try{
			const response = await API.post('checkout/cart/remove', productData)
			if(response.status === 200) {
				dispatch(fetchCartInfo(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}

export function updateCart(cart_id, quantity, callback = () => {}){
	return async dispatch => {
		let productData  = new URLSearchParams()
		productData.append('key', cart_id)
		productData.append('quantity', quantity)

		try{
			const response = await API.post('checkout/cart/update', productData)
			if(response.status === 200) {
				dispatch(fetchCartInfo(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}

export function wishToCart(product_id, quantity, callback = () => {}){
	return async dispatch => {
		let productData  = new URLSearchParams()
		productData.append('product_id', product_id)
		productData.append('quantity', quantity)

		try{
			const response = await API.post('checkout/cart/add', productData)
			if(response.status === 200) {
				dispatch(fetchCartInfo(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}

export function applyCoupon(couponData, callback = () => {}){
	return async dispatch => {
		try{
			const response = await API.post('extension/total/coupon/coupon', couponData)
			if(response.status === 200) {
				const info = {
					error: response.data.error?response.data.error:null
				}
				dispatch(couponSuccess(info))
				dispatch(fetchTotals(callback))
			}
		} catch(e){
			console.log(e)
		}
	}
}


