import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './AccountWish.scss'
import {RiLoginBoxLine, RiUserAddLine, RiLockPasswordLine, RiLogoutBoxRLine} from 'react-icons/ri'
import {FaRegUserCircle} from 'react-icons/fa'
import {BiHeartCircle} from 'react-icons/bi'
function AccountWish(props){

	const text = (prop) => {
		if(props.accountWish && props.accountWish.language){
			return ReactHtmlParser(props.accountWish.language[prop])
		}
	}

	return (
		<div
			className="account-wish main-theme-bg"
			onClick={() => { props.history.push('/wish') }}
		>
			{console.log(props)}
			{props.accountWish
				? <React.Fragment>
					<div className="profile-title-wrap">
						<h3 className={'profile-title'}>
							<BiHeartCircle style={{marginBottom: '6px', marginRight: '7px'}}/>
							{props.accountWish.heading_title}
						</h3>
					</div>
					<div className="wish-data-wrap">
						<div className="wish-data">
							<span className={'data-name'}>{text('text_total_product')}</span>
							<span className={'data-value'}>{props.accountWish.total_products?props.accountWish.total_products:0}</span>
						</div>
					{/*	<div className="profile-data">*/}
					{/*		<span className={'data-name'}>{text('entry_lastname')}</span>*/}
					{/*		<span className={'data-value'}>{props.accountWish.lastname}</span>*/}
					{/*	</div>*/}
					</div>
				</React.Fragment>
				: null
			}
		</div>
	)
}

export default withRouter(AccountWish)
