import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './AccountOrders.scss'
import {RiLoginBoxLine, RiUserAddLine, RiLockPasswordLine, RiLogoutBoxRLine} from 'react-icons/ri'
import {FaRegUserCircle} from 'react-icons/fa'
import {FiShoppingBag} from 'react-icons/fi'
function AccountOrders(props){

	const text = (prop) => {
		if(props.accountOrders && props.accountOrders.language){
			return ReactHtmlParser(props.accountOrders.language[prop])
		}
	}

	return (
		<div
			className="account-orders main-theme-bg"
			onClick={() => { props.history.push('/orders') }}
		>
			{console.log(props)}
			{props.accountOrders
				? <React.Fragment>
					<div className="profile-title-wrap">
						<h3 className={'profile-title'}>
							<FiShoppingBag style={{marginBottom: '6px', marginRight: '7px'}}/>
							{props.accountOrders.heading_title}
						</h3>
					</div>
					<div className="orders-data-wrap">
						<div className="orders-data">
							<span className={'data-name'}>{text('column_total')}</span>
							<span className={'data-value'}>{props.accountOrders.order_total?props.accountOrders.order_total:0}</span>
						</div>
					{/*	<div className="profile-data">*/}
					{/*		<span className={'data-name'}>{text('entry_lastname')}</span>*/}
					{/*		<span className={'data-value'}>{props.accountOrders.lastname}</span>*/}
					{/*	</div>*/}
					</div>
				</React.Fragment>
				: null
			}
		</div>
	)
}

export default withRouter(AccountOrders)
