import React, {useState, useEffect, Component} from 'react'
import {
    Container,
    Row,
    Col,
    Table,
    Button,
    NavLink,
    ButtonGroup,
    Tooltip,
    CustomInput,
    CustomInputGroup,
    Form,
    FormGroup, CardFooter, CardBody, Card, Label, Input, Collapse
} from 'reactstrap'
import Select from "react-select"
import './ShippingMethod.scss'
import CustomerForm from "../CustomerForm/CustomerForm";
import Message from "../ui/message/Message";
import classNames from "classnames";
import ReactHtmlParser from "react-html-parser";

function ShippingMethod(props){

    // const [init, setInit] = useState(false)
    // const initShippingMethod = () => {
    //     if(!init) {
    //         const addressForm = document.forms['shippingAddressForm']
    //         const shippingData = new FormData(addressForm)
    //         shippingData.append('shipping_method', props.form.code)
    //         const shippingAddress = props.session.shipping_address_id
    //         props.setShippingMethod(shippingData, shippingAddress)
    //         setInit(true)
    //         console.log('!!!! initShippingMethod', props.form.code)
    //     }
    // }

    const setShippingMethod = (value) => {
        const addressForm = document.forms['shippingAddressForm']
        const shippingData = new FormData(addressForm)
        shippingData.append('shipping_method', value)
        const shippingAddress = props.session.shipping_address_id?props.session.shipping_address_id:null
        props.setShippingMethod(shippingData, shippingAddress)
        console.log('!!!! setShippingMethod', shippingAddress)
    }


    const renderShippingMethodQuote = (method) => {

        return Object.keys(method.quote).map((index) => {
            const quote = method.quote[index]
            return (
                <div className={'shipping-method'} key={'quote_' + index}>
                    <div className={'shipping-method-name'}>
                        <CustomInput
                            type="radio"
                            defaultChecked={quote.code == props.form.code}
                            id={quote.code}
                            name="shipping_method"
                            value={quote.code}
                            label={ReactHtmlParser(quote.title)}
                            onChange={setShippingMethod.bind(this, quote.code)}
                        />

                        {props.form.shipping_logo[index] && !props.form.shipping_title_display
                            ? <img style="max-width:100%" src={props.form.shipping_logo[index]}
                                   alt={props.form.shipping_method.title} title={props.form.shipping_method.title}/>
                            : null
                        }
                    </div>
                    <div className={'shipping-method-quote'}>
                        <label htmlFor={quote.code}>{ReactHtmlParser(quote.text)}</label>
                    </div>
                </div>
            )
        })

    }

    const renderShippingMethods = () => {
        return Object.keys(props.form.shipping_methods).map((index) => {
            const method = props.form.shipping_methods[index]
            return (
                <React.Fragment key={'method_' + index}>
                    {props.form.shipping_title_display && false
                        ? <div class={'shipping-method-title'}>
                            <b>{ method.title }</b>
                          </div>
                        : null
                    }
                    { !method.error
                        ? renderShippingMethodQuote(method)
                        : <div className={'shipping-method-error'}>
                            <div class="error">
                                { method.error }
                            </div>
                          </div>
                    }
                </React.Fragment>
            )
        })
    }


    const shippingMethodSelect = () => {
        let optionsArray = []
        const currentOption = props.form.code
        props.form.shipping_methods.map((method, key) => {
            method.quote.map((quote, key) => {
                optionsArray.push({dataValue: quote.code, label: quote.title + '&nbsp;&nbsp;(' + quote.text + ')'})
            })
        })

        const index = optionsArray.findIndex(item => item['dataValue'] === currentOption )
        const valueKey = (index && currentOption && index + 1)?index:0

            return (
                <React.Fragment>
                    <Select
                        className='react-select-container'
                        classNamePrefix="react-select"
                        options={optionsArray}
                        value={optionsArray[valueKey]}
                        styles={props.selectColourStyles}
                        //onChange={setShippingMethod.bind(this, name)}
                    />
                    <Input
                        type="hidden"
                        name={'shipping_method'}
                        value={currentOption}
                        readOnly={true}
                    />
                </React.Fragment>
            )

    }

    return (
        <Container fluid={true}>
            <Row>
                <Col sm={'12'}>
                    {props.form.error_warning
                        ? <div className="alert alert-danger">{ReactHtmlParser(props.form.error_warning)}</div>
                        : null
                    }
                    {props.form.shipping_methods
                        ? <React.Fragment>
                             <p>{props.form.text_shipping_method}</p>
                             {props.form.shipping
                                ? <React.Fragment>
                                     <div
                                         className={classNames('shipping-method-table', {tableStriped: props.form.shipping_title_display})}
                                     >
                                         {renderShippingMethods()}
                                     </div>
                                  </React.Fragment>
                                : <React.Fragment>
                                    { shippingMethodSelect()}
                                </React.Fragment>
                            }
                        </React.Fragment>
                        : null
                    }
                    {/*{!props.session.shipping_method?initShippingMethod():null}*/}

                    {/*{% if delivery and (not delivery_delivery_time or delivery_delivery_time == '1' or delivery_delivery_time == '3') %}*/}
                    {/*<hr>*/}
                    {/*    <div{{ delivery_required ? ' class="required"' }}>*/}
                    {/*        <label class="control-label"><strong>{{ text_delivery }}</strong></label>*/}
                    {/*        {% if delivery_delivery_time == '1' %}*/}
                    {/*        <input type="text" name="delivery_date" value="{{ delivery_date }}" class="form-control date" readonly="true" style="background:#ffffff;" />*/}
                    {/*        {% else %}*/}
                    {/*        <input type="text" name="delivery_date" value="{{ delivery_date }}" class="form-control date" readonly="true" style="background:#ffffff;" />*/}
                    {/*        {% endif %}*/}
                    {/*        {% if delivery_delivery_time == '3' %}<br />*/}
                    {/*        <select name="delivery_time" class="form-control">{% for quickcheckout_delivery_time in delivery_times %}*/}
                    {/*            {% if quickcheckout_delivery_time[language_id] %}*/}
                    {/*            {% if delivery_time == quickcheckout_delivery_time[language_id] %}*/}
                    {/*            <option value="{{ quickcheckout_delivery_time[language_id] }}" selected="selected">{{ quickcheckout_delivery_time[language_id] }}</option>*/}
                    {/*            {% else %}*/}
                    {/*            <option value="{{ quickcheckout_delivery_time[language_id] }}">{{ quickcheckout_delivery_time[language_id] }}</option>*/}
                    {/*            {% endif %}*/}
                    {/*            {% endif %}*/}
                    {/*            {% endfor %}</select>*/}
                    {/*        {% endif %}*/}
                    {/*    </div>*/}
                    {/*    {% elseif delivery_delivery_time and delivery_delivery_time == '2' %}*/}
                    {/*    <input type="text" name="delivery_date" value="" class="hide" />*/}
                    {/*    <select name="delivery_time" class="hide"><option value=""></option></select>*/}
                    {/*    <strong>{{ text_estimated_delivery }}</strong><br />*/}
                    {/*    {{ estimated_delivery }}<br />*/}
                    {/*    {{ estimated_delivery_time }}*/}
                    {/*    {% else %}*/}
                    {/*    <input type="text" name="delivery_date" value="" class="hide" />*/}
                    {/*    <select name="delivery_time" class="hide"><option value=""></option></select>*/}
                    {/*    {% endif %}*/}

                </Col>
            </Row>
        </Container>
    )
}

export default ShippingMethod