import {
	FETCH_ACCOUNT_INFO_START, FETCH_ACCOUNT_INFO_SUCCESS, FETCH_ACCOUNT_INFO_ERROR,
	FETCH_ACCOUNT_EDIT_START, FETCH_ACCOUNT_EDIT_SUCCESS, FETCH_ACCOUNT_EDIT_ERROR,
	FETCH_ADDRESSES_START, FETCH_ADDRESSES_SUCCESS, FETCH_ADDRESSES_ERROR,
	FETCH_ORDERS_START, FETCH_ORDERS_SUCCESS, FETCH_ORDERS_ERROR,
	FETCH_REWARDS_START, FETCH_REWARDS_SUCCESS, FETCH_REWARDS_ERROR,
	//SEND_ACCOUNT_INFO_START, SEND_ACCOUNT_INFO_SUCCESS, SEND_ACCOUNT_INFO_ERROR
} from '../actions/actionTypes'
import Dashboard from "views/Dashboard.js";
import Icons from "../../views/Icons"
import Map from "views/Map.js";
import Notifications from "views/Notifications.js";
//import Rtl from "views/Rtl.js";
import TableList from "views/TableList.js"
import Typography from "views/Typography.js"
import UserProfile from "views/UserProfile.js"

import {Route} from "react-router-dom";
import React from "react";

let initalState = {
	account: {
		account: {},
		accountEdit: {},
		accountAddresses: {},
		accountOrders: {},
		accountRewards: {},
		routes:[
			{
				path: "account/dashboard",
				name: "Login",
				rtlName: " ",
				icon: "tim-icons icon-chart-pie-36",
				component: Dashboard,
				layout: "account",
			}
		]
	},

}

const pageComponent = (page) => {
	switch(page) {
		case 'account':
			return Dashboard
		case 'wishlist':
			return Icons
		case 'order':
			return TableList
		case 'address':
			return Typography
		case 'reward':
			return UserProfile
		case 'affiliate':
			return Dashboard
		case 'transaction':
			return Dashboard
		case 'card':
			return Dashboard
		case 'newsletter':
			return Dashboard
		case 'recurring':
			return Dashboard
		case 'download':
			return Dashboard
		case 'password':
			return Dashboard
		case 'return':
			return Dashboard
		default:
			return null
	}
}

const addComponents = (routes) =>{
	return routes.map((prop, key) => {
		if (prop.page) {
			prop.component = pageComponent(prop.page)
		}
	})
}

export default function accountReducer(state = initalState, action){
	switch(action.type){
		case FETCH_ACCOUNT_INFO_START:
			return{
				...state, loading: true
			}
		case FETCH_ACCOUNT_INFO_START:
			return{...state, loading: true}
		case FETCH_ACCOUNT_INFO_SUCCESS:
			if(action.routes) {
				addComponents(action.routes)
			}
			return {...state,
				loading: false,
				account: action.account,
				routes: action.routes
			}
		case FETCH_ACCOUNT_INFO_ERROR:
			return{...state, loading: false, error: action.error}

		case FETCH_ACCOUNT_EDIT_START:
			return{
				...state, loading: true
			}
		case FETCH_ACCOUNT_EDIT_SUCCESS:
			return{
				...state,
				loading: false,
				accountEdit: action.accountEdit
			}
		case FETCH_ACCOUNT_EDIT_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_ADDRESSES_START:
			return{
				...state, loading: true
			}
		case FETCH_ADDRESSES_SUCCESS:
			return{
				...state,
				loading: false,
				accountAddresses: action.accountAddresses
			}
		case FETCH_ADDRESSES_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_ORDERS_START:
			return{
				...state, loading: true
			}
		case FETCH_ORDERS_SUCCESS:
			return{
				...state,
				loading: false,
				accountOrders: action.orders
			}
		case FETCH_ORDERS_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_REWARDS_START:
			return{
				...state, loading: true
			}
		case FETCH_REWARDS_SUCCESS:
			return{
				...state,
				loading: false,
				accountRewards: action.rewards
			}
		case FETCH_REWARDS_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		// case SEND_ACCOUNT_INFO_START:
		// 	return{
		// 		...state, loading: true
		// 	}
		// case SEND_ACCOUNT_INFO_SUCCESS:
		// 	return{
		// 		...state,
		// 		loading: false,
		// 		errorsAuth: action.errorsAuth,
		// 		messagesAuth: action.messagesAuth
		// 	}
		// case SEND_ACCOUNT_INFO_ERROR:
		// 	return{
		// 		...state, loading: false, error: action.error
		// 	}

		default:
			return state
	}
}