import React, {useState} from 'react'
import {Container, Row, Col, Button, NavLink, ButtonGroup, Tooltip, Input, InputGroup, Form} from 'reactstrap'
import {FaInfoCircle, FaCheck} from 'react-icons/fa'
import './COD.scss'
import ReactHtmlParser from "react-html-parser";

function COD(props){

    return(
        <React.Fragment>
        <div className="buttons">
            <Input
                type="button"
                value={ReactHtmlParser(props.orderInfo.payment.button_confirm) }
                id="button-confirm"
                data-loading-text={props.orderInfo.payment.text_loading }
                className="btn theme-border-button btn btn-none"
                onClick={props.confirmPayment}
            />
        </div>
        </React.Fragment>
    )
}

export default COD