import {
	FETCH_WISH_INFO_START,
	FETCH_WISH_INFO_SUCCESS,
	FETCH_WISH_INFO_ERROR
} from '../actions/actionTypes'

let initalState = {
	wish:{language:{}},
	countWishProducts: 0,
	loading: false,
	error: null
}

export default function wishReducer(state = initalState, action){
	
	switch(action.type){
		case FETCH_WISH_INFO_START:
			return{
				...state, loading: true
			}
		case FETCH_WISH_INFO_SUCCESS:
			return{
				...state, loading: false, wish: action.wish, countWishProducts: action.countWishProducts
			}
		case FETCH_WISH_INFO_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		default:
			return state
	}
	
}