import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import {Container, Row, Col, Button, NavLink, ButtonGroup, Tooltip, Input, InputGroup, Form} from 'reactstrap'
import {FaInfoCircle, FaCheck} from 'react-icons/fa'
import './PaymentConfirm.scss'
import BankTransfer from './BankTransfer/BankTransfer'
import COD from './COD/COD'
import Apirone from "./Apirone/Apirone";
import Icons from "../../views/Icons";
import API from "../../utils/API";

class PaymentConfirm extends Component {
    state = {
        paymentData: null
    }


    async sendConfirmPayment(action) {
        const start = new Date()

        console.log('ACTION', action)
        if (action){
            try {
                const response = await API.post('extension/payment/' + action)
                console.log('SEND PAYMENT DATA response', response)
                if (response.status === 200) {
                    if(response.data.redirect){
                        this.props.fetchSuccessInfo(this.redirectSuccess)
                    } else {
                        this.setState({
                            paymentData: response.data
                        })
                    }
                    const end = new Date()
                    //console.log('SEND PAYMENT DATA time = ', end - start)
                }
            } catch (e) {
                console.log(e)
            }
        }
    }

    redirectSuccess = () =>{
        this.props.history.push('/success')
    }

    confirmPayment = () => {
        const action = this.props.orderInfo.payment.action
        this.sendConfirmPayment(action)
    }

    paymentComponent = () => {
        switch(this.props.orderInfo.payment.payment_method) {
            case 'bank_transfer':
                return ( <BankTransfer {...this.props} confirmPayment={this.confirmPayment} /> )
            case 'cod':
                return ( <COD {...this.props} confirmPayment={this.confirmPayment} /> )
            case 'apirone':
                return ( <Apirone {...this.props} confirmPayment={this.confirmPayment} paymentData={this.state.paymentData}/> )

            default:
                return null
        }
    }

   render() {
        return (
            <div className={'payment-module-wrap'}>
                {this.paymentComponent()}
            </div>
        )
   }
}

export default withRouter(PaymentConfirm)