import React, {useState} from 'react'
import {
    Container,
    Row,
    Col,
    Button,
    NavLink,
    ButtonGroup,
    Tooltip,
    CustomInput,
    CustomInputGroup,
    Form,
    FormGroup, CardFooter, CardBody, Card, Label, Input, Collapse
} from 'reactstrap'
import Select from "react-select"
import './PaymentMethod.scss'
import CustomerForm from "../CustomerForm/CustomerForm";
import Message from "../ui/message/Message";
import classNames from "classnames";
import ReactHtmlParser from "react-html-parser";

function PaymentMethod(props){
    // const [init, setInit] = useState(false)
    // const initPaymentMethod = () => {
    //     if(!init) {
    //         const addressForm = document.forms['paymentAddressForm']
    //         const paymentData = new FormData(addressForm)
    //         paymentData.append('payment_method', props.form.code)
    //         const paymentAddress = props.session.payment_address_id
    //         props.setPaymentMethod(paymentData, paymentAddress)
    //         setInit(true)
    //         console.log('!!!! initShippingMethod', paymentAddress)
    //     }
    // }

    const setPaymentMethod = (value) => {
        const addressForm = document.forms['paymentAddressForm']
        const paymentData = new FormData(addressForm)
        paymentData.append('payment_method', value)
        const paymentAddress = props.session.payment_address_id
        props.setPaymentMethod(paymentData, paymentAddress)
        console.log('!!!! setShippingMethod', paymentAddress)
    }

    const renderPaymentMethods = () => {
        return Object.keys(props.form.payment_methods).map((index) => {
            const method = props.form.payment_methods[index]
            return (
                <div className={'payment-method'} key={'payment_method_' + index}>
                    <div className={'payment-method-name'}>
                        <CustomInput
                            type="radio"
                            defaultChecked={method.code == props.form.code}
                            id={method.code}
                            name="payment_method"
                            value={method.code}
                            label={ReactHtmlParser(method.title)}
                            onChange={setPaymentMethod.bind(this, method.code)}
                        />
                        {props.form.payment_logo[method.code]
                            ? <img src={props.form.payment_logo[method.code]} alt={method.title} title={method.title}/>
                            : null
                        }
                    </div>
                    <div className={classNames("payment-description" ,{show:(props.session && props.session.payment_method && props.session.payment_method.code && method.code == props.session.payment_method.code)})} style={{padding: '10px 0 10px 30px'}}>
                        {ReactHtmlParser(method.description)}
                    </div>
                </div>
            )
        })
    }

    const paymentMethodSelect = () => {
        let optionsArray = []
        const currentOption = props.form.code
        props.form.payment_methods.map((method, key) => {
            optionsArray.push({dataValue: method.code, label: method.title})
        })

        const index = optionsArray.findIndex(item => item['dataValue'] === currentOption )
        const valueKey = (index && currentOption && index + 1)?index:0

        return (
            <React.Fragment>
                <Select
                    className='react-select-container'
                    classNamePrefix="react-select"
                    options={optionsArray}
                    value={optionsArray[valueKey]}
                    styles={props.selectColourStyles}
                    //onChange={setPaymentMethod.bind(this, name)}
                />
                <Input
                    type="hidden"
                    name={'payment_method'}
                    value={currentOption}
                    readOnly={true}
                />
            </React.Fragment>
        )

    }

    return (
        <Container fluid={true}>
            <Row>
                <Col sm={'12'}>
                    {props.form.error_warning
                        ? <div className="alert alert-danger">{ReactHtmlParser(props.form.error_warning)}</div>
                        : null
                    }
                    <div className={classNames({hidden: !props.form.payment_module})}>
                    {props.form.payment_methods
                        ? <React.Fragment>
                            <p>{ReactHtmlParser(props.form.text_payment_method)}</p>
                            {props.form.payment
                                ? <React.Fragment>
                                    <div className={classNames('payment-method-table', {tableStriped: props.form.payment_title_display})}>
                                        {renderPaymentMethods()}
                                    </div>
                                </React.Fragment>
                                : <React.Fragment>
                                    { paymentMethodSelect()}
                                </React.Fragment>
                            }
                        </React.Fragment>
                        : null
                    }
                    {/*{!props.session.payment_method?initPaymentMethod():null}*/}

                    {/*{% if survey_survey %}*/}
                    {/*{% if payment_module and payment %}*/}
                    {/*<hr>*/}
                    {/*    {% endif %}*/}
                    {/*    <div{{ survey_required ? ' class="required"' }}>*/}
                    {/*        <label class="control-label"><strong>{{ text_survey }}</strong></label>*/}
                    {/*        {% if survey_type %}*/}
                    {/*        <select name="survey" class="form-control">*/}
                    {/*            <option value=""></option>*/}
                    {/*            {% for survey_answer in survey_answers %}*/}
                    {/*            {% if survey_answer[language_id] %}*/}
                    {/*            {% if survey == survey_answer[language_id] %}*/}
                    {/*            <option value="{{ survey_answer[language_id] }}" selected="selected">{{ survey_answer[language_id] }}</option>*/}
                    {/*            {% else %}*/}
                    {/*            <option value="{{ survey_answer[language_id] }}">{{ survey_answer[language_id] }}</option>*/}
                    {/*            {% endif %}*/}
                    {/*            {% endif %}*/}
                    {/*            {% endfor %}</select><br />*/}
                    {/*        {% else %}*/}
                    {/*        <textarea name="survey" class="form-control" rows="1">{{ survey }}</textarea><br /><br />*/}
                    {/*        {% endif %}*/}
                    {/*    </div>*/}
                    {/*    {% else %}*/}
                    {/*    <textarea name="survey" class="hide">{{ survey }}</textarea>*/}
                    {/*    {% endif %}*/}

                    {/*    {% if field_comment.display %}*/}
                    {/*    <strong>{% if field_comment.required %}<span class="required">*</span> {% endif %}{{ text_comments }}</strong>*/}
                    {/*    <textarea name="comment" rows="8" class="form-control" placeholder="{{ field_comment.placeholder ? field_comment.placeholder }}">{{ comment ? comment : field_comment.default }}</textarea>*/}
                    {/*    {% else %}*/}
                    {/*    <textarea name="comment" class="hide"></textarea>*/}
                    {/*    {% endif %}*/}

                    </div>
                </Col>
            </Row>
        </Container>
    )
}

export default PaymentMethod