import React, {useState} from 'react'
import {Container, Row, Col, Button, NavLink, ButtonGroup, Tooltip, Input, InputGroup, Form} from 'reactstrap'
import {FaInfoCircle, FaCheck} from 'react-icons/fa'
import './BankTransfer.scss'
import ReactHtmlParser from "react-html-parser";

function BankTransfer(props){

    return(
        <React.Fragment>
            <h3>{ReactHtmlParser(props.orderInfo.payment.text_instruction)}</h3>
            <p><b>{ReactHtmlParser(props.orderInfo.payment.text_description)}</b></p>
            <div className="well well-sm">
                <p>{ReactHtmlParser(props.orderInfo.payment.bank)}</p>
                <p>{ReactHtmlParser(props.orderInfo.payment.text_payment)}</p>
            </div>
            <div className="buttons">
                <Input
                    type="button"
                    value={props.orderInfo.payment.button_confirm }
                    id="button-confirm"
                    data-loading-text={props.orderInfo.payment.text_loading }
                    className="btn theme-border-button btn btn-none"
                    onClick={props.confirmPayment}
                />
            </div>
        </React.Fragment>
    )
}

export default BankTransfer