import React, {useState} from 'react'
import ReactHtmlParser from 'react-html-parser'
import {Route, withRouter, Switch, Redirect, useLocation} from "react-router-dom"
import {
    Container,
    Row,
    Col,
    Input,
    Label,
    Button,
    NavLink,
    ButtonGroup,
    CustomInput,
    Form,
    FormFeedback
} from 'reactstrap';
import classNames from "classnames";
import Coupon from "../Coupon/Coupon";
import PaymentConfirm from "../PaymentConfirm/PaymentConfirm";
import './CheckoutInfo.scss'

function CheckoutInfo(props){
    const text = (prop) => {
        if(props.language){
            return props.language[prop]
        }
    }

    const [validateRegister, setValidateRegister] = useState(null)
    const setThisValidateRegister = (valid, callback=()=>{}) => {
        setValidateRegister(valid)
        callback()
    }
    const validateRegisterData = (callback=()=>{}) => {
         const create = document.getElementById('create')
         if(create && create.value === 'true') {
             setValidateRegister(false)
             const paymentForm = document.forms['paymentAddressForm']
             const paymentData = new FormData(paymentForm)
             const type = 'register'
             props.validateCustomerData(type, paymentData, setThisValidateRegister, callback)
         } else {
             setValidateRegister(true)
         }
    }

    const [validatePayment, setValidatePayment] = useState(null)
    const setThisValidatePayment = (valid, callback=()=>{}) => {
        setValidatePayment(valid)
        callback()
    }
    const validatePaymentData = (callback=()=>{}) => {
        setValidatePayment(false)
        const paymentForm = document.forms['paymentAddressForm']
        const paymentData = new FormData(paymentForm)
        const type = props.checkout.guest?'guest':'payment_address'
        props.validateCustomerData(type, paymentData, setThisValidatePayment, callback)
    }

    const [validateShipping, setValidateShipping] = useState(null)
    const setThisValidateShipping = (valid, callback=()=>{}) => {
        setValidateShipping(valid)
        callback()
    }
    const validateShippingData = (callback=()=>{}) => {
        const shipping = document.getElementById('shipping')
        if(shipping && shipping.value === 'true') {
            setValidateShipping(true)
        } else {
            setValidateShipping(false)
            const shippingForm = document.forms['shippingAddressForm']
            const shippingData = new FormData(shippingForm)
            const type = props.checkout.guest?'guest_shipping':'shipping_address'
            props.validateCustomerData(type, shippingData, setThisValidateShipping, callback)
        }
    }

    const [validatePaymentMethod, setValidatePaymentMethod] = useState(null)
    const setThisValidatePaymentMethod = (valid, callback=()=>{}) => {
        setValidatePaymentMethod(valid)
        callback()
    }
    const validatePaymentMethodData = (callback=()=>{}) => {
        setValidatePaymentMethod(false)
        const paymentMethodForm = document.forms['paymentMethodForm']
        const paymentMethodData = new FormData(paymentMethodForm)
        const type = 'payment_method'
        props.validateCustomerData(type, paymentMethodData, setThisValidatePaymentMethod, callback)
    }

    const [validateShippingMethod, setValidateShippingMethod] = useState(null)
    const setThisValidateShippingMethod = (valid, callback=()=>{}) => {
        setValidateShippingMethod(valid)
        callback()
    }
    const validateShippingMethodData = (callback=()=>{}) => {
        setValidateShippingMethod(false)
        const shippingMethodForm = document.forms['shippingMethodForm']
        const shippingMethodData = new FormData(shippingMethodForm)
        const type = 'shipping_method'
        props.validateCustomerData(type, shippingMethodData, setThisValidateShippingMethod, callback)
    }

    const [validateTerms, setValidateTerms] = useState(null)
    const setThisValidateTerms = (valid, callback=()=>{}) => {
        setValidateTerms(valid)
        callback()
    }
    const validateTermsData = (callback=()=>{}) => {
        setValidateTerms(false)
        const termsForm = document.forms['termsForm']
        const termsData = new FormData(termsForm)
        const type = 'terms'
        props.validateCustomerData(type, termsData, setThisValidateTerms, callback)
    }

    const [checkPaymentData, setCheckPaymentData] = useState(false)
    const checkPayment = () => {
        props.orderConfirm()
        setCheckPaymentData(true)
    }

    const validateCreateOrder = () => {
        setCheckPaymentData(false)
        validatePaymentData()
        setValidatePaymentMethod(null)
        setValidateShippingMethod(null)
        setValidateRegister(null)
        setValidatePayment(null)
        setValidateShipping(null)
        setValidateTerms(null)

        const create = document.getElementById('create')
        if(create){
            validateRegisterData()
        }
    }

    const renderTotals = () => {
        if(props.totals) {
            return Object.keys(props.totals).map((index) => {
                const total = props.totals[index]
                return (
                    <div className="total-wrap" key={'fr-checkout-total-' + index} >
                        <span className="total-title-sub"><b>{total.title}</b></span>
                        <span className="total-textt-sub">{total.text}</span>
                    </div>
                )
            })
        }
    }

    const renderPayment = () => {
        return (
            <div id="payment" className="text-left" style={{ display: 'none' }}>
                    12345
            </div>
        )
    }

    const [agree, setAgree] = useState(null)

    const renderTerms = () => {
        return (
            <React.Fragment>
                {validatePayment
                    ? validateRegister === null ? validateRegisterData() : null
                    : null
                }
                {validateRegister
                    ? validateShipping === null ? validateShippingData() : null
                    : null
                }
                {validateShipping
                    ? validateShippingMethod === null ? validateShippingMethodData() : null
                    : null
                }
                {validateShippingMethod
                    ? validatePaymentMethod === null ? validatePaymentMethodData() : null
                    : null
                }
                {validatePaymentMethod
                    ? validateTerms === null ? validateTermsData() : null
                    : null
                }

                {/*{validateRegister*/}
                {/*    ? validatePaymentMethod === null ? validatePaymentMethodData() : null*/}
                {/*    : null*/}
                {/*}*/}
                {/* {validatePaymentMethod*/}
                {/*    ? validateShippingMethod === null ? validateShippingMethodData() : null*/}
                {/*    : null*/}
                {/*}*/}
                {/*{validateShippingMethod*/}
                {/*    ? validatePayment === null ? validatePaymentData() : null*/}
                {/*    : null*/}
                {/*}*/}
                {/*{validatePayment*/}
                {/*    ? validateShipping === null ? validateShippingData() : null*/}
                {/*    : null*/}
                {/*}*/}
                {/*{validateShipping*/}
                {/*    ? validateTerms === null ? validateTermsData() : null*/}
                {/*    : null*/}
                {/*}*/}
                {/*{validateTerms*/}
                {/*    ? validateRegister === null ? validateRegisterData() : null*/}
                {/*    : null*/}
                {/*}*/}

                {validateRegister &&
                validatePayment &&
                validateShipping &&
                validatePaymentMethod &&
                validateShippingMethod &&
                validateTerms
                    ? (!checkPaymentData? checkPayment(): null )
                    : null
                }
                {!props.orderInfo || !Object.keys(props.orderInfo).length
                    ? <React.Fragment>
                        {props.terms.rules && props.terms.rules.display
                            ? <div className="terms">
                                <Form name={'termsForm'}>
                                    {agree === null ? setAgree(!!props.terms.rules.default) : null}
                                    <label>
                                        {props.terms.text_agree
                                            ? <div className="total-wrap">
                                                <CustomInput
                                                    type="checkbox"
                                                    defaultChecked={!!props.terms.rules.default}
                                                    className={classNames({required: props.terms.rules.required})}
                                                    value={agree}
                                                    id={"agree-reg"}
                                                    name={"agree"}
                                                    label={ReactHtmlParser(props.terms.text_agree)}
                                                    onChange={() => {
                                                        setAgree(!agree)
                                                    }}
                                                />
                                              </div>
                                            : null
                                        }
                                    </label>
                                </Form>
                                <Input type="hidden"
                                       invalid={props.errorTerms && props.errorTerms.agree ? true : false}/>
                                <FormFeedback style={{
                                    textAlign: 'center',
                                    width: '100%',
                                    marginBottom: '10px'
                                }}>{props.errorTerms && props.errorTerms.agree ? props.errorTerms.agree : false}</FormFeedback>
                                <Button
                                    type="button"
                                    id="button-payment-method"
                                    color="none"
                                    className="btn theme-border-button"
                                    data-loading-text={props.language.text_loading}
                                    checked={props.disable_button ? "checked" : false}
                                    onClick={validateCreateOrder}
                                >
                                    {props.language.button_place_order}
                                </Button>
                            </div>
                            : null
                        }
                      </React.Fragment>
                    :  <PaymentConfirm
                            orderInfo={props.orderInfo}
                            confirmPayment={props.confirmPayment}
                            fetchSuccessInfo={props.fetchSuccessInfo}
                        />
                }
            </React.Fragment>
        )
    }

    const renderCoupon = () => {
        if(props.cart.modules) {
            return Object.keys(props.cart.modules).map((index) => {
                const module = props.cart.modules[index]
                if (module.coupon !== null) {
                    return (
                        <Coupon
                            key={'cart-module-' + index}
                            language={props.cart.language}
                            coupon={module.coupon}
                            couponError={props.couponError}
                            applyCoupon={props.applyCoupon}
                        />
                    )
                }
            })
        } else {
            return null
        }
    }

    const renderModules = () => {
        return (
            <React.Fragment>
                { props.checkout.coupon_module || props.checkout.reward_module || props.checkout.voucher_module
                    ? <div className="module-info-wrap">
                        {props.checkout.coupon_module
                            ? renderCoupon()
                            : null
                        }
                        {props.checkout.reward_module ? null : null}
                        {props.checkout.voucher_module ? null : null}
                    </div>
                    : null
                }
            </React.Fragment>
         )
    }

    return(
        <div id="checkout-info-table-wrap" className="main-theme-bg">
            {props.totals && props.terms
                ? <div id="checkout-info-table">
                    <div className={props.coupon ? 'coupon-success product-cart-info' : 'product-cart-info'}>
                        {renderPayment()}
                        {renderModules()}
                        <div className="module-info-wrap">
                            {renderTotals()}
                            {renderTerms()}
                        </div>
                    </div>
                </div>
                : null
            }
        </div>
    )
}

export default withRouter(CheckoutInfo)