import React, {Component, useState} from 'react'
import ruru from "../../../image/ruru.png"
import dede from "../../../image/dede.png"
import engb from "../../../image/engb.png"
import { ButtonDropdown, Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import "./Language.scss"

function Language(props) {

    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggle = () => setDropdownOpen(prevState => !prevState);

    const change = (code) =>{
        toggle()
        props.changeLanguage(code)
    }

    const currentLanguage = () => {
        if(props.languages.languages.length) {
            return props.languages.languages.map((language, index) => {
                if (language.code === props.languages.code) {
                    return (
                        <React.Fragment key={'cl' + index}>
                            {language.code === 'ru-ru' ?
                                <img src={ruru} alt={language.name} title={language.name}/> : null}
                            {language.code === 'de-de' ?
                                <img src={dede} alt={language.name} title={language.name}/> : null}
                            {language.code === 'en-gb' ?
                                <img src={engb} alt={language.name} title={language.name}/> : null}
                            <span key={'lang-name' + index} id={language.code} className="lang-name">{language.code_name}</span>
                            <span key={'caret-down' + index} className="caret-down"> </span>
                        </React.Fragment>
                    )
                } else {
                    return null
                }
            })
        }
    }

    const menuLanguage = () => {
        if(props.languages.languages.length) {
            return props.languages.languages.map((language, index) => {
                if (language.code !== props.languages.code) {
                    return (
                        <React.Fragment key={'lm' + index}>
                            {language.code !== props.languages.languages.code
                                ? <li
                                    key={'lang-li' + index}
                                    onClick={() => {change(language.code)}}
                                >
                                    {language.code === 'ru-ru' ?
                                        <img src={ruru} alt={language.name} title={language.name}/> : null}
                                    {language.code === 'de-de' ?
                                        <img src={dede} alt={language.name} title={language.name}/> : null}
                                    {language.code === 'en-gb' ?
                                        <img src={engb} alt={language.name} title={language.name}/> : null}
                                    <span key={'lang-sel' + index} className="language-select">{language.code_name}</span>
                                </li>
                                : null
                            }
                        </React.Fragment>
                    )
                } else {
                    return null
                }
            })
        }
    }

    return(
        <>
            { props.languages.languages.length > 1
                ? <Dropdown className="header-language-dropdown" isOpen={dropdownOpen} toggle={toggle}>
                    <DropdownToggle>
                        { currentLanguage() }
                    </DropdownToggle>
                    <DropdownMenu>
                        { menuLanguage() }
                    </ DropdownMenu>
                </Dropdown>
                : null
            }
        </>
    )

}
export default Language