import {FETCH_MAIN_INFO_START, FETCH_MAIN_INFO_SUCCESS, FETCH_MAIN_INFO_ERROR, SEND_MAIN_INFO_START, SEND_MAIN_INFO_SUCCESS, SEND_MAIN_INFO_ERROR} from '../actions/actionTypes'

let initalState = {
	
	formControls:{
		firstname:{
			value: '',
			type: 'text',
			label: '',
			placeholder: '',
			errorMessage: '',
			valid: true,
			touched: false,
			validation:{
				required: true,
				minLength: 2,
				maxLength: 30
			}
		},
		lastname:{
			value: '',
			type: 'text',
			label: '',
			placeholder: '',
			errorMessage: '',
			valid: true,
			touched: false,
			validation:{
				required: true,
				minLength: 2,
				maxLength: 30
			}
		},
		email:{
			value: '',
			type: 'email',
			label: 'E-mail',
			placeholder: 'E-mail',
			errorMessage: '',
			valid: true,
			touched: false,
			validation:{
				required: true,
				email: true
			}
		},
		telephone: {
			value: '',
			type: 'tel',
			label: '',
			placeholder: '',
			errorMessage: '',
			valid: true,
			touched: false,
			mask: "+37\\5 99 999 99 99",
			validation: {
				required: true,
				tel: true
			}
		},
	},
	loading: false,
	error: null,
	needupdate: true
	
}

export default function mainInfoReducer(state = initalState, action){
	
	switch(action.type){
		case FETCH_MAIN_INFO_START:
			return{
				...state, loading: true
			}
		case FETCH_MAIN_INFO_SUCCESS:

			initalState = { 
				...state, formControls: action.formControls, needupdate: false
			}
			return{
				...state, loading: false, formControls: action.formControls, needupdate: false
			}
		case FETCH_MAIN_INFO_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case SEND_MAIN_INFO_START:
			return{
				...state, loading: true
			}
		case SEND_MAIN_INFO_SUCCESS:

			initalState = { 
				...state, formControls: action.formControls, needupdate: false
			}
			return{
				...state, loading: false, formControls: action.formControls, needupdate: false
			}
		case SEND_MAIN_INFO_ERROR:
			return{
				...state, loading: false, error: action.error
			}
			
		default:
			return state
	}
	
}