import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './AccountCart.scss'
import {RiLoginBoxLine, RiUserAddLine, RiLockPasswordLine, RiLogoutBoxRLine} from 'react-icons/ri'
import {FaRegUserCircle} from 'react-icons/fa'
import {TiShoppingCart} from 'react-icons/ti'
function AccountCart(props){

	const text = (prop) => {
		if(props.accountCart && props.accountCart.language){
			return ReactHtmlParser(props.accountCart.language[prop])
		}
	}

	return (
		<div
			className="account-cart main-theme-bg"
			onClick={() => { props.history.push('/cart') }}
		>
			{console.log(props)}
			{props.accountCart
				? <React.Fragment>
					<div className="profile-title-wrap">
						<h3 className={'profile-title'}>
							<TiShoppingCart style={{marginBottom: '6px', marginRight: '7px'}}/>
							{props.accountCart.heading_title}
						</h3>
					</div>
					<div className="cart-data-wrap">
						<div className="cart-data">
							<span className={'data-name'}>{text('text_total_product')}</span>
							<span className={'data-value'}>{props.accountCart.count_products?props.accountCart.count_products:0}</span>
						</div>
					{/*	<div className="profile-data">*/}
					{/*		<span className={'data-name'}>{text('entry_lastname')}</span>*/}
					{/*		<span className={'data-value'}>{props.accountCart.lastname}</span>*/}
					{/*	</div>*/}
					</div>
				</React.Fragment>
				: null
			}
		</div>
	)
}

export default withRouter(AccountCart)
