                        <Row>
                            <Col xs="6">
                                <Input
                                    type="text"
                                    name="coupon"
                                    defaultValue={ props.coupon }
                                    placeholder={text('entry_coupon')}
                                    id="input-coupon"
                                    className="form-control"
                                />
                            </Col>
                            <Col xs="6">
                                <span
                                    className="button-coupon"
                                    id="button-coupon"
                                    onClick={applyCoupon}
                                >
                                    <span><FaCheck /> &nbsp;</span>
                                    <span>{text('button_coupon')}</span>
                                </span>
                            </Col>
                        </Row>
