import API from "../../utils/API"
import {
	FETCH_SESSION_START,
	FETCH_SESSION_SUCCESS,
	FETCH_SESSION_ERROR
} from './actionTypes'
import {fetchShippingMethod, setShippingMethod, fetchPaymentMethod, setPaymentMethod, fetchTotals} from './cart'

export function fetchSession(sessionData = null, shippingCode = null, paymentCode = null){
	const start = new Date()
	return async dispatch => {
		dispatch(fetchSessionStart())
        if(!sessionData) {
            try {
                const response = await API.get('common/session')
                console.log('SESSION Response', response)
                const info = {
                    session: response.data.session,
                }

                if ( !response.data.session.payment_methods || !response.data.session.payment_method) {
					setTimeout(() => {
						const addressForm = document.forms['paymentAddressForm']
						if(addressForm) {
							const paymentData = new FormData(addressForm)
							dispatch(fetchPaymentMethod(paymentData, (response.data.session.payment_address_id && !response.data.session.guest) ? response.data.session.payment_address_id : null))
						}
					}, 100)
                }
				if(!response.data.session.shipping_methods || !response.data.session.shipping_method){
					//setTimeout(() => {
						const shippingAddressForm = document.forms['shippingAddressForm']
						if(shippingAddressForm) {
							const shippingData = new FormData(shippingAddressForm)
							dispatch(fetchShippingMethod(shippingData, (response.data.session.shipping_address_id && !response.data.session.guest) ? response.data.session.payment_address_id : null))
						}
					//}, 100)
				}

                dispatch(fetchSessionSuccess(info))
                const end = new Date()
                //console.log('SESSION time = ', end - start)

            } catch (e) {
                console.log(e)
                dispatch(fetchSessionError(e))
            }
        } else {
			const info = {
				session: sessionData,
			}

			if(!sessionData.payment_method && paymentCode){
				setTimeout(() => {
					const addressForm = document.forms['paymentAddressForm']
					if(addressForm) {
						const paymentData = new FormData(addressForm)
						paymentData.append('payment_method', paymentCode)
						dispatch(setPaymentMethod(paymentData, sessionData.payment_address_id?sessionData.payment_address_id:null ))
						setTimeout(() => dispatch(fetchTotals()), 100)
					}
				}, 100)
			}
			if(!sessionData.shipping_method && shippingCode){
				//setTimeout(() => {
					const shippingAddressForm = document.forms['shippingAddressForm']
					if(shippingAddressForm) {
						const shippingData = new FormData(shippingAddressForm)
						shippingData.append('shipping_method', shippingCode)
						dispatch(setShippingMethod(shippingData, sessionData.shipping_address_id?sessionData.shipping_address_id:null))
						setTimeout(() => dispatch(fetchTotals()), 100)

					}
				//}, 100)
			}
			dispatch(fetchSessionSuccess(info))
			//setTimeout(() => dispatch(fetchTotals()), 200)
		}
	}
}

export function fetchSessionStart(){
	return {
		type: FETCH_SESSION_START
	}
}
export function fetchSessionSuccess(info){
	return {
		type: FETCH_SESSION_SUCCESS,
		session: info.session,
	}
}
export function fetchSessionError(e){
	return {
		type: FETCH_SESSION_ERROR,
		error: e
	}
}