import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './Addresses.scss'
import {RiLoginBoxLine, RiUserAddLine, RiLockPasswordLine, RiLogoutBoxRLine} from 'react-icons/ri'
import {FaRegUserCircle} from 'react-icons/fa'
import {RiMapPinUserLine} from 'react-icons/ri'
function addresses(props){

	const text = (prop) => {
		if(props.accountAddresses && props.accountAddresses.language){
			if(props.accountAddresses.language[prop]) {
				return ReactHtmlParser(props.accountAddresses.language[prop])
			} else if(props.languageMain[prop]){
				return ReactHtmlParser(props.languageMain[prop])
			} else {
				return null
			}
		}
	}

	const renderAddresses = () => {
		return props.accountAddresses.addresses.map((result, index) => {
			return (
				<tr key={'account-address-' + index}>
					<td className="text-left">{ReactHtmlParser(result.address)}</td>
					<td className="text-right">
						<a href={result.update}
						   className="but-sm theme-border-button">{text('button_edit')}</a> &nbsp;
						<a href={result.delete}
						   className="but-sm theme-border-button">{text('button_delete')}</a>
					</td>
				</tr>
			)
		})
	}
	const renderAddressBlock = () => {
		return (
			<div className="table">
				<table className="table table-bordered table-hover">
					{renderAddresses()}
				</table>
			</div>
		)
	}

	return (
		<div
			className="account-addresses main-theme-bg"
			onClick={() => { props.history.push('/addresses') }}
		>
			{console.log(props)}
			{props.accountAddresses
				? <React.Fragment>
					<div className="profile-title-wrap">
						<h3 className={'profile-title'}>
							<RiMapPinUserLine style={{marginBottom: '6px', marginRight: '7px'}}/>
							{props.accountAddresses.heading_title}
						</h3>
					</div>
					{props.accountAddresses.addresses
						? renderAddressBlock()
						: <p>{text('text_empty')}</p>
					}
					<div className="buttons">
						<a href={props.accountAddresses.back} className="but-sm theme-border-button" style={{marginRight: '15px'}}>{text('button_back')}</a>
						<a href={props.accountAddresses.add} className="but-sm theme-border-button">{text('button_new_address')}</a>
					</div>
				</React.Fragment>
				: null
			}
		</div>
	)
}

export default withRouter(addresses)
