import {
	FETCH_CART_INFO_START, FETCH_CART_INFO_SUCCESS, FETCH_CART_INFO_ERROR, APPLY_COUPON_SUCCESS,
	FETCH_SUCCESS_INFO_START, FETCH_SUCCESS_INFO_SUCCESS, FETCH_SUCCESS_INFO_ERROR,
	FETCH_SHIPPING_METHOD_START, FETCH_SHIPPING_METHOD_SUCCESS, FETCH_SHIPPING_METHOD_ERROR,
	SET_SHIPPING_METHOD_START, SET_SHIPPING_METHOD_SUCCESS, SET_SHIPPING_METHOD_ERROR,
	FETCH_TERMS_START, FETCH_TERMS_SUCCESS, FETCH_TERMS_ERROR,
	FETCH_TOTALS_START, FETCH_TOTALS_SUCCESS, FETCH_TOTALS_ERROR,
	VALIDATE_CUSTOMER_DATA_START, VALIDATE_CUSTOMER_DATA_SUCCESS, VALIDATE_CUSTOMER_DATA_ERROR, VALIDATE_PAYMENT_DATA_SUCCESS, VALIDATE_SHIPPING_DATA_SUCCESS, VALIDATE_REGISTER_DATA_SUCCESS,
	VALIDATE_PAYMENT_METHOD_DATA_SUCCESS, VALIDATE_SHIPPING_METHOD_DATA_SUCCESS, VALIDATE_TERMS_DATA_SUCCESS,
	FETCH_PAYMENT_METHOD_START, FETCH_PAYMENT_METHOD_SUCCESS, FETCH_PAYMENT_METHOD_ERROR,
	SET_PAYMENT_METHOD_START, SET_PAYMENT_METHOD_SUCCESS, SET_PAYMENT_METHOD_ERROR,
	SAVE_CUSTOMER_DATA_START, SAVE_CUSTOMER_DATA_SUCCESS, SAVE_CUSTOMER_DATA_ERROR,
	FETCH_CUSTOMER_OPTIONS_START, FETCH_CUSTOMER_OPTIONS_SUCCESS, FETCH_CUSTOMER_OPTIONS_ERROR,
	ORDER_CONFIRM_START, ORDER_CONFIRM_SUCCESS, ORDER_CONFIRM_ERROR
} from '../actions/actionTypes'

let initalState = {
	cart: {},
	checkout: {},
	terms:{},
	totals:{},
	errorShipping: {},
	errorPayment: {},
	errorRegister: {},
	errorPaymentMethod: {},
	errorShippingMethod: {},
	errorTerms: {},
	shippingMethod: {},
	paymentMethod: {},
	countries: [],
	customerGroups: [],
	countCartProducts: 0,
	couponError: null,
	loading: false,
	error: null,
	orderInfo:{},
	success: {}
}

export default function cartReducer(state = initalState, action){
	
	switch(action.type){
		case FETCH_CART_INFO_START:
			return{
				...state, loading: true
			}
		case FETCH_CART_INFO_SUCCESS:
			return{
				...state,
				loading: false,
				cart: action.cart,
				countCartProducts: action.countCartProducts,
				checkout: action.checkout,
				orderInfo:{}
			}
		case FETCH_CART_INFO_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_SUCCESS_INFO_START:
			return{
				...state, loading: true
			}
		case FETCH_SUCCESS_INFO_SUCCESS:
			return{
				...state,
				loading: false,
				success: action.success
			}
		case FETCH_SUCCESS_INFO_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case APPLY_COUPON_SUCCESS:
			return{
				...state, couponError: action.error
			}
		case FETCH_TERMS_START:
			return{
				...state, loading: true
			}
		case FETCH_TERMS_SUCCESS:
			return{
				...state,
				loading: false,
				terms: action.terms
			}
		case FETCH_TERMS_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_TOTALS_START:
			return{
				...state, loading: true
			}
		case FETCH_TOTALS_SUCCESS:
			return{
				...state,
				loading: false,
				totals: action.totals,
				countCartProducts: action.countCartProducts
			}
		case FETCH_TOTALS_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case VALIDATE_CUSTOMER_DATA_START:
			return{
				...state, loading: true
			}
		case VALIDATE_CUSTOMER_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				// totals: action.totals,
				// countCartProducts: action.countCartProducts
			}
		case VALIDATE_PAYMENT_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				errorPayment: action.error
			}
		case VALIDATE_SHIPPING_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				errorShipping: action.error
			}
		case VALIDATE_REGISTER_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				errorRegister: action.error
			}
		case VALIDATE_PAYMENT_METHOD_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				errorPaymentMethod: action.error
			}
		case VALIDATE_SHIPPING_METHOD_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				errorShippingMethod: action.error
			}
		case VALIDATE_TERMS_DATA_SUCCESS:
			return{
				...state,
				loading: false,
				errorTerms: action.error
			}
		case VALIDATE_CUSTOMER_DATA_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_SHIPPING_METHOD_START:
			return{
				...state, loading: true
			}
		case FETCH_SHIPPING_METHOD_SUCCESS:
			return{
				...state,
				loading: false,
				shippingMethod: action.shippingMethod
			}
		case FETCH_SHIPPING_METHOD_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case ORDER_CONFIRM_START:
			return{
				...state, loading: true
			}
		case ORDER_CONFIRM_SUCCESS:
			return{
				...state,
				loading: false,
				orderInfo: action.orderInfo
			}
		case ORDER_CONFIRM_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_PAYMENT_METHOD_START:
			return{
				...state, loading: true
			}
		case FETCH_PAYMENT_METHOD_SUCCESS:
			return{
				...state,
				loading: false,
				paymentMethod: action.paymentMethod
			}
		case FETCH_PAYMENT_METHOD_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case SET_SHIPPING_METHOD_START:
			return{
				...state, loading: true
			}
		case SET_SHIPPING_METHOD_SUCCESS:
			return{
				...state,
				loading: false,

			}
		case SET_SHIPPING_METHOD_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case SET_PAYMENT_METHOD_START:
			return{
				...state, loading: true
			}
		case SET_PAYMENT_METHOD_SUCCESS:
			return{
				...state,
				loading: false,

			}
		case SET_PAYMENT_METHOD_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case SAVE_CUSTOMER_DATA_START:
			return{
				...state, loading: true
			}
		case SAVE_CUSTOMER_DATA_SUCCESS:
			return{
				...state,
				loading: false,

			}
		case SAVE_CUSTOMER_DATA_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		case FETCH_CUSTOMER_OPTIONS_START:
			return{
				...state, loading: true
			}
		case FETCH_CUSTOMER_OPTIONS_SUCCESS:
			return{
				...state,
				loading: false,
				countries: action.countries,
				customerGroups: action.customerGroups
			}
		case FETCH_CUSTOMER_OPTIONS_ERROR:
			return{
				...state, loading: false, error: action.error
			}
		default:
			return state
	}
	
}