import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
import { Container, Row, Col } from 'reactstrap';
import {Button, NavLink, ButtonGroup} from "reactstrap";
import './AccountAddresses.scss'
import {RiLoginBoxLine, RiUserAddLine, RiLockPasswordLine, RiLogoutBoxRLine} from 'react-icons/ri'
import {FaRegUserCircle} from 'react-icons/fa'
import {RiMapPinUserLine} from 'react-icons/ri'
function accountAddresses(props){

	const text = (prop) => {
		if(props.accountAddresses && props.accountAddresses.language){
			return ReactHtmlParser(props.accountAddresses.language[prop])
		}
	}

	return (
		<div
			className="account-addresses main-theme-bg"
			onClick={() => { props.history.push('/addresses') }}
		>
			{console.log(props)}
			{props.accountAddresses
				? <React.Fragment>
					<div className="profile-title-wrap">
						<h3 className={'profile-title'}>
							<RiMapPinUserLine style={{marginBottom: '6px', marginRight: '7px'}}/>
							{props.accountAddresses.heading_title}
						</h3>
					</div>
					<div className="addresses-data-wrap">
						<div className="addresses-data">
							<span className={'data-name'}>{text('text_address_book')}</span>
							<span className={'data-value'}>{props.accountAddresses.addresses.length}</span>
						</div>
					{/*	<div className="profile-data">*/}
					{/*		<span className={'data-name'}>{text('entry_lastname')}</span>*/}
					{/*		<span className={'data-value'}>{props.accountAddresses.lastname}</span>*/}
					{/*	</div>*/}
					</div>
				</React.Fragment>
				: null
			}
		</div>
	)
}

export default withRouter(accountAddresses)
