import React, {useState} from 'react'
import {
    Container,
    Row,
    Col,
    Button,
    NavLink,
    ButtonGroup,
    Tooltip,
    CustomInput,
    CustomInputGroup,
    Form,
    FormGroup, CardFooter, CardBody, Card, Label, Input, Collapse, FormFeedback
} from 'reactstrap'
import Select from "react-select"
import './CheckoutGuest.scss'
import CustomerForm from "../CustomerForm/CustomerForm";
import Message from "../ui/message/Message";
import classNames from "classnames";
import ReactHtmlParser from "react-html-parser";

function CheckoutGuest(props){

    const [createAccount, setCreateAccount] = useState(() => {return !!props.form.create_account})
    const [newsletter, setNewsletter] = useState(() => {return !!(props.form.field_newsletter && props.form.field_newsletter.default)})
    const [agree, setAgree] = useState(() => {return !!(props.form.field_agree && props.form.field_agree.default)})
    const [shipping, setShipping] = useState(() => {return !!(props.form.shipping_address && props.form.shipping_checkbox.default)})
    const setShippingCheck = () => {
        setShipping(!shipping)
        props.toggleShippingAddressBlock()
        props.saveData('payment')
    }

    return (
        <Container fluid={true}>
            <CustomerForm
                addressType={'payment'}
                form={props.form}
                countries={props.countries}
                fetchCountryZones={props.fetchCountryZones}
                saveData={props.saveData}
                selectColourStyles={props.selectColourStyles}
                language={props.language}
                error={props.error}
            />
            <Row>
                <div className={classNames({notShippingRegister:(!props.form.shipping_checkbox.display && props.form.field_register.display)})}> </div>
                <Col sm={'12'}>
                    <div className={classNames('guest-register-block', {shippingRegister:(props.form.shipping_checkbox.display || props.form.field_register.display)})}>
                         <React.Fragment>
                            {props.form.field_register.display
                                ? <React.Fragment>
                                    {!props.form.guest_checkout || props.form.field_register.required
                                        ? null //<input type="checkbox" name="create_account" value="1" id="create" className="hide" checked="checked"/>
                                        : <React.Fragment>
                                            <CustomInput
                                                type="checkbox"
                                                defaultChecked={createAccount}
                                                value={createAccount}
                                                id={"create"}
                                                name={"create_account"}
                                                label={ReactHtmlParser(props.language.text_create_account)}
                                                onChange={() => {setCreateAccount(!createAccount); props.saveData('payment')}}
                                            />
                                            <Input
                                                type="hidden"
                                                invalid={props.errorRegister && props.errorRegister && props.errorRegister.exist?true:false}
                                            />
                                            <FormFeedback>{props.errorRegister && props.errorRegister.exist?props.errorRegister.exist:false}</FormFeedback>
                                          </React.Fragment>
                                    }

                                    <Collapse
                                        isOpen={createAccount}
                                        className="panel-collapse"
                                    >
                                        <div id="create_account">
                                            <Row>
                                                <Col
                                                    xs={{size: 12}} sm={{size: 6}}
                                                    className={'custom-field'}
                                                >
                                                    <FormGroup>
                                                        <label
                                                            htmlFor={'guest-password'}
                                                            className={'required'}
                                                        >
                                                            { props.language.entry_password }
                                                        </label>
                                                        <Input
                                                            invalid={props.errorRegister && props.errorRegister.password?true:false}
                                                            id={'guest-password'}
                                                            className={'form-control'}
                                                            name={"password"}
                                                            type="password"
                                                        />
                                                        <FormFeedback>{props.errorRegister && props.errorRegister.password?props.errorRegister.password:false}</FormFeedback>
                                                    </FormGroup>
                                                </Col>

                                                <Col
                                                    xs={{size: 12}} sm={{size: 6}}
                                                    className={'custom-field'}
                                                >
                                                    <FormGroup>
                                                        <label
                                                            htmlFor={'guest-confirm'}
                                                            className={'required'}
                                                        >
                                                            { props.language.entry_confirm }
                                                        </label>
                                                        <Input
                                                            invalid={props.errorRegister && props.errorRegister.confirm?true:false}
                                                            id={'guest-confirm'}
                                                            className={'form-control'}
                                                            name={"confirm"}
                                                            type="password"
                                                        />
                                                        <FormFeedback>{props.errorRegister && props.errorRegister.confirm?props.errorRegister.confirm:false}</FormFeedback>
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col xs={{size: 12}} sm={{size: 6}}
                                                     className={'custom-field'}>
                                                      <CustomInput
                                                            type="checkbox"
                                                            defaultChecked={newsletter}
                                                            className={classNames({required: (props.form.register.field_newsletter && props.form.register.field_newsletter.required), displayNone: (props.form.register.field_newsletter && !props.form.register.field_newsletter.display) })}
                                                            value={newsletter}
                                                            id={"newsletter"}
                                                            name={"newsletter"}
                                                            label={ReactHtmlParser(props.form.register.entry_newsletter)}
                                                            onChange={() => {setNewsletter(!newsletter); props.saveData('payment')}}
                                                      />

                                                    <React.Fragment>
                                                        {props.form.register.text_agree
                                                            ?  <React.Fragment>
                                                                 <CustomInput
                                                                    type="checkbox"
                                                                    defaultChecked={agree}
                                                                    //className={classNames({required: props.form.field_rules.required, displayNone: !props.form.field_rules.display })}
                                                                    value={agree}
                                                                    id={"agree-reg"}
                                                                    name={"agree"}
                                                                    label={ReactHtmlParser(props.form.register.text_agree)}
                                                                    onChange={() => {setAgree(!agree); props.saveData('payment')}}
                                                                 />
                                                                 <Input
                                                                     type="hidden"
                                                                     invalid={props.errorRegister && props.errorRegister.agree?true:false}
                                                                 />
                                                                 <FormFeedback>{props.errorRegister && props.errorRegister.agree?props.errorRegister.agree:false}</FormFeedback>
                                                            </React.Fragment>
                                                            : null
                                                        }
                                                    </React.Fragment>
                                                </Col>
                                            </Row>
                                        </div>
                                    </Collapse>
                                  </React.Fragment>
                                : <input type="checkbox" name="create_account" value="1" id="create" className="hide"/>
                            }
                         </React.Fragment>
                         <React.Fragment>
                         {props.form.shipping_checkbox.display && parseInt(props.form.show_shipping_address)
                            ? <React.Fragment>
                                 {/*{props.isOpenShippingAddressBlock && shipping ? props.setIsOpenShipping(false) : null}*/}
                                 {/*{props.isOpenShippingAddressBlock && shipping ? props.toggleShippingAddressBlock() : null}*/}
                                 <CustomInput
                                     type="checkbox"
                                     defaultChecked={shipping}
                                     value={shipping}
                                     id={"shipping"}
                                     name={"shipping_address"}
                                     label={ReactHtmlParser(props.language.entry_shipping)}
                                     onChange={() => setShippingCheck()}
                                 />
                              </React.Fragment>
                            : <input type="hidden" name="shipping_address" value="1" id="shipping" checked="checked" className="hide"/>
                         }
                         </React.Fragment>
                    </div>
                </Col>
            </Row>
        </Container>
    )
}

export default CheckoutGuest