import React, {Component, useState} from 'react'
import {
    Container,
    Row,
    Col,
    Button,
    NavLink,
    ButtonGroup,
    Tooltip,
    FormFeedback,
    CustomInput,
    CustomInputGroup,
    Form,
    FormGroup, CardFooter, CardBody, Card, Label, Input, Collapse
} from 'reactstrap'
import Select from "react-select"
import './CustomerForm.scss'
import Message from "../ui/message/Message";
import classNames from "classnames"
import API from "../../utils/API"

class CustomerForm extends Component {

    state = {
        zones : [],
        currentCountry : this.props.form['country_id'],
        currentZone: this.props.form['zone_id'],
        currentCustomerGroup: this.props.form['customer_group_id']
    }

    async fetchCountryZones(countryId){
        const start = new Date()
        try{
            const responce = await API.get('checkout/checkout/country&country_id=' + countryId)
            console.log('ZONES Responce', responce)
            if(responce.status === 200) {
                let countryZones = []
                responce.data.zone.map((countryZone) => {
                    countryZones.push({dataValue: countryZone.zone_id, label: countryZone.name})
                })
                const end = new Date()
                //console.log('ZONES time = ', end - start)
                const currentZone = countryZones[0]?countryZones[0].dataValue:null
                this.setState({
                    zones: countryZones,
                    currentZone: currentZone,
                    currentCountry: countryId
                })

                this.props.saveData(this.props.addressType)
                setTimeout( this.fetchShippingMethod.bind(this), 300)
                this.fetchShippingMethod()
                this.fetchPaymentMethod()
            }
        } catch(e){
            console.log(e)
        }
    }

    fetchPaymentMethod(){
        const paymentForm = document.forms['paymentAddressForm']
        const paymentData = new FormData(paymentForm)
        if(this.props.fetchPaymentMethod) {
            this.props.fetchPaymentMethod(paymentData)
        }
    }

    fetchShippingMethod(){
        const shippingForm = document.forms['shippingAddressForm']
        const shippingData = new FormData(shippingForm)
        if (this.props.fetchShippingMethod){
            this.props.fetchShippingMethod(shippingData)
        }
    }

    // const [zones, setZones] = useState(() => {return fetchCountryZones(props.form['country_id'])})
    // const [currentCountry, setCurrentCountry] = useState(() => {return props.form['country_id']})
    // const [currentZone, setCurrentZone] = useState(() => {return props.form['zone_id']})
    // const [currentCustomerGroup, setCurrentCustomerGroup] = useState(() => {return props.form['customer_group_id']})


   onSelectChange = (name, value)=> {
        switch (name) {
            case 'country':
                //const zones = this.fetchCountryZones(value.dataValue)
                this.fetchCountryZones(value.dataValue)

                break
            case 'zone':
                this.setState({
                    currentZone: value.dataValue
                })
                this.props.saveData(this.props.addressType)
                break
            case 'customer_group':
                this.setState({
                    CustomerGroup: value.dataValue
                })
                this.props.saveData(this.props.addressType)
                break
            default:
                break
        }
        return null
    }

    customSelect = (name, optionsArray, currentOption) => {
        let index = null
        if(optionsArray.length) {
            index = optionsArray.findIndex(item => item['dataValue'] === currentOption)
        }
        const valueKey = (index && currentOption && index + 1) ? index : 0
        return (
            <React.Fragment>
                <Select
                    className='react-select-container'
                    classNamePrefix="react-select"
                    options={optionsArray}
                    value={optionsArray[valueKey]}
                    styles={this.props.selectColourStyles}
                    onChange={this.onSelectChange.bind(this, name)}
                />
                <Input
                    type="hidden"
                    name={name + '_id'}
                    value={currentOption}
                    readOnly={true}
                />
            </React.Fragment>
        )

    }

    renderCustomSelect = (name) => {
        if (this.props.form['field_' + name].display) {
            return (
                <React.Fragment>
                        { name === 'country' && this.props.countries
                            ? <Col sm={'6'}>
                                <FormGroup key={'guest_field_' + name}>
                                    <Label
                                        className="control-label"
                                        htmlFor="CustomInput-payment-country"
                                        className={classNames({required: this.props.form['field_' + name].required})}
                                    >
                                        {this.props.language['entry_' + name]}
                                    </Label>
                                    {this.customSelect('country', this.props.countries, this.state.currentCountry)}
                                </FormGroup>
                            </Col>
                            : null
                        }
                        { name === 'zone' && this.state.zones.length
                            ? <Col sm={'6'}>
                                <FormGroup key={'guest_field_' + name}>
                                    <Label
                                        className="control-label"
                                        htmlFor="CustomInput-payment-country"
                                        className={classNames({required: this.props.form['field_' + name].required})}
                                    >
                                        {this.props.language['entry_' + name]}
                                    </Label>
                                    {this.customSelect('zone', this.state.zones, this.state.currentZone)}
                                </FormGroup>
                            </Col>
                            : null
                        }
                        { name === 'customer_group' && this.props.customerGroups
                            ? <Col sm={'6'}>
                                <FormGroup key={'guest_field_' + name}>
                                    <Label
                                        className="control-label"
                                        htmlFor="CustomInput-payment-country"
                                        className={classNames({required: this.props.form['field_' + name].required})}
                                    >
                                        {this.props.language['entry_' + name]}
                                    </Label>
                                    {this.customSelect('customer_group', this.props.customerGroups, this.state.currentCustomerGroup)}
                                </FormGroup>
                            </Col>
                            : null
                        }
                </React.Fragment>
            )
        } else {
            return (
                <Input
                    type="hidden"
                    name={name + '_id'}
                    value={name ==='country' ? this.state.currentCountry: name === 'zone' ? this.state.currentZone : name === 'customer_group' ? this.state.currentCustomerGroup : null}
                    readOnly={true}
                />
            )
        }
    }

    renderField = (name) => {
        if (this.props.form['field_' + name].display) {
            return (
                <Col sm={'6'}>
                    <FormGroup>
                        <label htmlFor={'field_' + name} className={classNames({required:this.props.form['field_' + name].required})}>
                            {this.props.language['entry_' + name]}
                        </label>
                        <CustomInput
                            invalid={(this.props.error && this.props.error[name])?true:false}
                            className={'form-control'}
                            id={'field_' + name}
                            name={name}
                            defaultValue={
                                this.props.form && this.props.form.customer && this.props.form.customer[name]
                                    ?this.props.form.customer[name]
                                    :this.props.form && this.props.form[name]
                                        ?this.props.form[name]
                                        :this.props.form['field_' + name].default
                            }
                            placeholder={this.props.form['field_' + name].placeholder}
                            type={'text'}
                            onChange={() => this.props.saveData(this.props.addressType)}
                        />

                        <FormFeedback>{(this.props.error && this.props.error[name])?this.props.error[name]:false}</FormFeedback>
                    </FormGroup>
                </Col>
            )
        } else {
            return (
                <Input
                    name={name}
                    value={this.props.form['field_' + name].default}
                    type={'hidden'}
                    readOnly={true}
                />
            )
        }
    }

    customFieldSelect = (custom_field) => {
        if(custom_field.custom_field_value) {
            let optionsArray = []
            let valueKey = 0

            custom_field.custom_field_value.map((custom_field_value, key) => {
                optionsArray.push({ dataValue: custom_field_value.custom_field_id, label: custom_field_value.name })
                if (this.props.form.guest_custom_field[custom_field.custom_field_id]
                    && this.props.form.guest_custom_field[custom_field.custom_field_id] === custom_field_value.custom_field_value_id){
                    valueKey = key
                }
            })

            return (
                <Select
                    className='react-select-container'
                    classNamePrefix="react-select"
                    options={optionsArray}
                    value={optionsArray[valueKey]}
                    name={custom_field.name}
                    styles={this.props.selectColourStyles}
                    onChange={() => this.props.saveData(this.props.addressType)}
                />
            )
        } else {
            return null
        }
    }

    renderCustomFieldSelect = (custom_field) => {
        return (
            <React.Fragment>
                {custom_field.custom_field_value.length
                    ? <Col sm={'6'}>
                        <FormGroup
                            key={'guest_custom_field_' + custom_field.name}
                        >
                            <Label className="control-label"
                               htmlFor={'input-payment-custom-field' + custom_field.custom_field_id}
                               className={classNames({required: this.props.form['field_' + custom_field.name].required})}
                            >
                                { custom_field.name }
                            </Label>
                            {this.customFieldSelect(custom_field)}
                        </FormGroup>
                    </Col>
                    : null
                }
            </React.Fragment>
        )
    }

    renderCustomRadioField = (custom_field) => {
        if (custom_field.custom_field_value) {
            let checked = false

            custom_field.custom_field_value.map((custom_field_value, key) => {
                if (this.props.form.guest_custom_field[custom_field.custom_field_id]
                    && this.props.form.guest_custom_field[custom_field.custom_field_id] === custom_field_value.custom_field_value_id) {
                    checked = 'checked'
                }
                return (
                    <label
                        key={'radio-field-' + custom_field.custom_field_id}
                        htmlFor={'input-payment-custom-field' + custom_field.custom_field_id}
                        className={classNames({required:this.props.form['field_' + custom_field.name].required})}
                    >
                        <CustomInput
                            type="radio"
                            checked={checked}
                            value={custom_field_value.custom_field_value_id}
                            id={'input-payment-custom-field' + custom_field.custom_field_id}
                            name={custom_field[custom_field.location][custom_field.custom_field_id]}
                            label={custom_field.name}
                            onChange={() => this.props.saveData(this.props.addressType)}
                        />
                    </label>
                )
            })
        } else {
            return null
        }
    }

    renderCustomCheckboxField = (custom_field) => {
        if (custom_field.custom_field_value) {
            let checked = false

            custom_field.custom_field_value.map((custom_field_value, key) => {
                if (this.props.form.guest_custom_field[custom_field.custom_field_id]
                    && this.props.form.guest_custom_field[custom_field.custom_field_id].includes(custom_field_value.custom_field_value_id)
                ) {
                    checked = 'checked'
                }

                return (
                    <div className="checkbox">
                        <label
                            key={'radio-field-' + custom_field.custom_field_id}
                            htmlFor={'input-payment-custom-field' + custom_field.custom_field_id}
                            className={classNames({required:this.props.form['field_' + custom_field.name].required})}
                        >
                            <CustomInput
                                type="checkbox"
                                checked={checked}
                                value={custom_field_value.custom_field_value_id}
                                id={'input-payment-custom-field' + custom_field.custom_field_id}
                                name={custom_field[custom_field.location][custom_field.custom_field_id]}
                                label={custom_field.name}
                                onChange={() => this.props.saveData(this.props.addressType)}
                            />
                        </label>
                    </div>
                )
            })
        } else {
            return null
        }
    }

    renderCustomTextField = (custom_field) => {
        let value = null
        if (this.props.form.guest_custom_field[custom_field.custom_field_id]) {
            value = this.props.form.guest_custom_field[custom_field.custom_field_id]
        } else {
            value = custom_field.value
        }

        return (
            <Col sm={'6'}
                 className={'custom-field'}
                 id={'payment-custom-field' + custom_field.custom_field_id}
            >
                <FormGroup>
                    <label
                        htmlFor={'input-payment-custom-field' + custom_field.custom_field_id}
                        className={classNames({required:this.props.form['field_' + custom_field.name].required})}
                    >
                        { custom_field.name }
                    </label>
                    <Input
                        className={'form-control'}
                        id={'nput-payment-custom-field' + custom_field.custom_field_id}
                        name={custom_field[custom_field.location][custom_field.custom_field_id]}
                        value={value}
                        placeholder={custom_field.name}
                        type={custom_field.type}
                        onChange={() => this.props.saveData(this.props.addressType)}
                    />
                </FormGroup>
            </Col>
        )
    }

    renderCustomTextareaField = (custom_field) => {
        let value = null
        if (this.props.form.guest_custom_field[custom_field.custom_field_id]) {
            value = this.props.form.guest_custom_field[custom_field.custom_field_id]
        } else {
            value = custom_field.value
        }

        return (
            <Col sm={'6'}
                 className={'custom-field'}
                 id={'payment-custom-field' + custom_field.custom_field_id}
            >
                <FormGroup>
                    <label
                        htmlFor={'input-payment-custom-field' + custom_field.custom_field_id}
                        className={classNames({required:this.props.form['field_' + custom_field.name].required})}
                    >
                        { custom_field.name }
                    </label>
                    <Input
                        className={'form-control'}
                        id={'nput-payment-custom-field' + custom_field.custom_field_id}
                        name={custom_field[custom_field.location][custom_field.custom_field_id]}
                        placeholder={custom_field.name}
                        rows="5"
                        type="textarea"
                        onChange={() => this.props.saveData(this.props.addressType)}
                    >
                        { value }
                    </Input>
                </FormGroup>
            </Col>
        )
    }

    renderCustomFileField = (custom_field) => {
        let value = null
        if (this.props.form.guest_custom_field[custom_field.custom_field_id]) {
            value = this.props.form.guest_custom_field[custom_field.custom_field_id]
        } else {
            value = custom_field.value
        }

        return (
            <Col sm={'6'}
                 className={'custom-field'}
                 id={'payment-custom-field' + custom_field.custom_field_id}
            >
                <FormGroup>
                    <label
                        htmlFor={'input-payment-custom-field' + custom_field.custom_field_id}
                        className={classNames({required:this.props.form['field_' + custom_field.name].required})}
                    >
                        { custom_field.name }
                    </label>
                    <CustomInput
                        className={'form-control'}
                        id={'input-payment-custom-field' + custom_field.custom_field_id}
                        name={custom_field[custom_field.location][custom_field.custom_field_id]}
                        placeholder={custom_field.name}
                        value={value}
                        type="file"
                        onChange={() => this.props.saveData(this.props.addressType)}
                    />
                </FormGroup>
            </Col>
        )
    }


    renderFields = () => {
        if(this.props.form.fields) {
            const fields = this.props.form.fields
            return fields.map((field, key) => {
                if(field == 'country') {
                    return (
                        <React.Fragment key={'guest-field-country' + key}>
                            {this.renderCustomSelect(field)}
                        </React.Fragment>
                    )
                } else if (field == 'zone'){
                    return (
                        <React.Fragment key={'guest-field-zone' + key}>
                            {this.renderCustomSelect(field)}
                        </React.Fragment>
                    )
                } else if (field == 'customer_group'){
                    return (
                        <React.Fragment key={'guest-field-customer_group' + key}>
                            { this.props.customerGroups && this.props.customerGroups.length > 1
                                ? this.renderCustomSelect(field)
                                : <Input
                                    type={'hidden'}
                                    name={'customer_group_id'}
                                    value={this.props.customerGroups?this.props.form.customerGroups[0].customer_group_id:0}
                                />
                            }
                        </React.Fragment>
                    )
                } else {
                    return (
                        <React.Fragment key={'guest-field-' + field + '-' + key}>
                            {this.renderField(field)}
                        </React.Fragment>
                    )
                }
            })
        } else {
            return null
        }
    }

    renderCustomFields  = () => {
        if(this.props.form.custom_fields) {
            const fields = this.props.form.custom_fields
            return fields.map((custom_field, key) => {
                if(custom_field.location == 'account' || custom_field.location == 'address') {
                    if (custom_field.type == 'select'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomFieldSelect(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'radio'){
                        return (
                            <div className={'radio'} key={'custom-field-' + key}>
                               {this.renderCustomRadioField(custom_field)}
                            </div>
                        )
                    } else if(custom_field.type == 'checkbox'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomCheckboxField(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'text'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomTextField(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'textarea'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomTextareaField(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'file'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomFileField(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'date'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomFileField(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'time'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomFileField(custom_field)}
                            </React.Fragment>
                        )
                    } else if(custom_field.type == 'datetime'){
                        return (
                            <React.Fragment key={'custom-field-' + key}>
                                {this.renderCustomFileField(custom_field)}
                            </React.Fragment>
                        )
                    }
                }
            })
        } else {
            return null
        }
    }

    UNSAFE_componentWillMount(){
        if(!this.state.zones.length) {
            this.fetchCountryZones(this.props.form['country_id'])
        }
    }

    render() {
        return (
            <Row>
                {this.renderFields()}
                {this.renderCustomFields()}
            </Row>

        )
    }
}

export default CustomerForm